import { APPLICANT_TYPE, APPLICANT_TYPE_LABEL } from "utils/Constant";
import { ISummaryDistanceDetailsTableRowData } from "./Sections/DistanceDetails/DistanceDetails";
import { ISummaryFamilyAndHouseHoldDetails } from "./Sections/FamilyAndHouseHoldDetails/FamilyAndHouseHoldDetails";
import { ISummaryWriteUpProfileDetails } from "./Sections/WriteUpOnProfile/WriteUpOnProfile";

export const updateFamilyOrHouseholdDetails = (data: any): ISummaryFamilyAndHouseHoldDetails[] => {
  const pdList: ISummaryFamilyAndHouseHoldDetails[] = [];
  data?.forEach((record: any) => {
    const pd: ISummaryFamilyAndHouseHoldDetails = {
      sourceDescription: `${
        APPLICANT_TYPE_LABEL?.[
          record?.applicant?.applicantType as keyof typeof APPLICANT_TYPE_LABEL
        ]??""
      } | ${record?.applicant?.fullName??""}`,
      smFamilyType:record?.smFamilyType??"",
      noOfHouseholdMembers:record?.noOfHouseholdMembers??"",
      noOfDependentMembers:record?.noOfDependentMembers??"",
      smOwnershipOfCurrentResidence:record?.smOwnershipOfCurrentResidence??"",
      areaOfCurrentResidence:record?.areaOfCurrentResidence??"",
      noYearsInCurrentCity:record?.noYearsInCurrentCity??"",
      neighbourhoodDescription:record?.neighbourhoodDescription??"",
      storyboard:record?.storyboard??"",
    };
    pdList.push(pd);
  });

  return pdList;
};


