import AutoComplete from "components/AutoComplete/AutoComplete";
import DeviationTable from "components/DeviationTable/DeviationTable";
import { Column, EditableComponentRef } from "components/EditableTableV2";
import ErrorDialog from "components/ErrorDialog/ErrorDialog";
import InputTextArea from "components/InputTextArea/InputTextArea";
import SimpleDropDown from "components/SimpleDropDown";
import Text from "components/Text/Text";
import i18n from "i18n";
import { ToastPositionType } from "primereact/toast";
import React, { ReactNode, useEffect, useRef, useState } from "react";
import { useParams } from "react-router-dom";
import StaticMasterDataService from "services/masterdata/StaticMaster/StaticMasterDataService";
import CamService from "services/transaction/CamService";
import SimpleReactValidator from "simple-react-validator";
import {
  getStandardValidator,
  messageReplace,
  parseStandardAPIErrorMessage,
  validateGetAPIResponse,
} from "utils/CommonUtil";
import { INPUT_STYLE_TYPE, TYPE_OF_DEVIATION } from "utils/Constant";

interface IDropDownOptions {
  name: string;
  code: string;
  uid: string;
}

interface IDropdownList {
  deviationType: IDropDownOptions[];
  deviationForAutomatic: IDropDownOptions[];
  deviationCategory: IDropDownOptions[];
  deviationLevel: IDropDownOptions[];
}

export type TDeviationType = "TYPE_OF_DEVIATION_AUTOMATIC" | "TYPE_OF_DEVIATION_MANUAL";

interface IPageObj {
  dropdownOptions: IDropdownList;
}

const initialPageOj: IPageObj = {
  dropdownOptions: {
    deviationType: [],
    deviationForAutomatic: [],
    deviationCategory: [],
    deviationLevel: [],
  },
};

interface IProps {
  data: {
    camStageUid: string;
  };
}

const Deviations = (props: IProps) => {
  const { data } = props;
  const params: { loanUid?: string } = useParams();

  /*
    -------------------------------------------------------
      State
    -------------------------------------------------------
  */

  const [pageObj, setPageObj] = useState<IPageObj>({ ...initialPageOj });

  const [position, setPosition] = useState<ToastPositionType | undefined>("bottom-right");
  const [showDialogAPIError, setShowDialogAPIError] = useState(false);
  const [errorMessage, setErrorMessage] = useState<any>("");
  const [, forceUpdate] = useState<boolean>(false);

  // ref
  const tableRef = React.useRef<EditableComponentRef>(null);
  const toastMessageRef = useRef(null);
  const simpleValidator = useRef(
    new SimpleReactValidator({
      validators: { ...getStandardValidator() },
      ...messageReplace(),
      autoForceUpdate: { forceUpdate: () => forceUpdate((prev) => !prev) },
    })
  );
  // --------------------------- global variables ---------------------- //

  /*
     -------------------------------------------------------
       useEffect
     -------------------------------------------------------
   */

  useEffect(() => {
    getDeviationTypeOptions();
    getDeviationCategory();
    getDeviationLevel();
  }, []);

  useEffect(() => {
    if (errorMessage) {
      setShowDialogAPIError(true);
    }
  }, [errorMessage]);

  useEffect(() => {
    if (data.camStageUid) {
      tableRef?.current?.search();
    }
  }, [data.camStageUid]);

  /*
    -------------------------------------------------------
      Service Calls
    -------------------------------------------------------
  */

  const getDeviationTypeOptions = () => {
    StaticMasterDataService.getAll(0, 10, "staticMasterType.code=TYPE_OF_DEVIATION&")
      .then((res) => {
        if (validateGetAPIResponse(res)) {
          updateDropdownOptions("deviationType", res?.data?.results);
        }
      })
      .catch((err) => {
        console.log(err);
        setErrorMessage(parseStandardAPIErrorMessage(err));
      });
  };

  const getDeviationCategory = () => {
    CamService.getDeviationCategories(0, 30, "")
      .then((res) => {
        if (validateGetAPIResponse(res)) {
          updateDropdownOptions("deviationCategory", res?.data?.results);
        }
      })
      .catch((err) => {
        console.log(err);
        setErrorMessage(parseStandardAPIErrorMessage(err));
      });
  };

  const getDeviationLevel = () => {
    CamService.getDeviationLevel(0, 30, "")
      .then((res) => {
        if (validateGetAPIResponse(res)) {
          updateDropdownOptions("deviationLevel", res?.data?.results);
        }
      })
      .catch((err) => {
        console.log(err);
        setErrorMessage(parseStandardAPIErrorMessage(err));
      });
  };

  /*
    -------------------------------------------------------
      Handlers
    -------------------------------------------------------
  */

  /* 
    --------------------------------------------------------
    Validators:
    --------------------------------------------------------
  */

  /*
    -------------------------------------------------------
      Button Click Handlers
    -------------------------------------------------------
  */

  /*
    -------------------------------------------------------
      Helper Functions
    -------------------------------------------------------
  */

  const updateDropdownOptions = (key: keyof IDropdownList, options: any[]) => {
    setPageObj((prevState: any) => ({
      ...prevState,
      dropdownOptions: {
        ...prevState?.dropdownOptions,
        [key]: options ?? [],
      },
    }));
  };

  /*
    -------------------------------------------------------
      Sectional Rendering
    -------------------------------------------------------
  */

  const manualDeviationsTemplate = (item: any) => {
    if (item?.name === undefined) {
      return (
        <div className="flex flex-col items-center justify-center h-39 pointer-events-none cursor-default">
          <Text className="text-primary-labelColor mt-2" label="label.noDataFound" />
        </div>
      );
    }
    return (
      <div className="flex flex-col">
        <Text className="text-primary-labelColor" label={item?.name} />
      </div>
    );
  };

  const renderHeaderTypeOfDeviation = () => {
    return (
      <div className="flex items-center">
        <Text label={i18n.t("label.cam.recommendation.table.typeofdeviation")} />
      </div>
    );
  };

  const renderTypeOfDeviations = (rowData: any) => {
    return (
      <div className="h-24">
        <SimpleDropDown
          placeholder="placeholder.legal.deviationType"
          name={`typeOfDeviation${rowData?.uid}`}
          value={rowData?.smDeviationType}
          variant={INPUT_STYLE_TYPE.PRIMARY}
          options={pageObj.dropdownOptions.deviationType}
          optionLabel="name"
          optionValue="code"
          hideLabel
          disabled
        />
      </div>
    );
  };

  const renderHeaderDeviationCategory = () => {
    return (
      <div className="flex items-center">
        <Text label={i18n.t("label.cam.recommendation.table.deviationCategory")} />
      </div>
    );
  };

  const renderDeviationCategory = (rowData: any): ReactNode => {
    return (
      <div className="h-24">
        <SimpleDropDown
          name={`deviationCat${rowData.uid}`}
          options={pageObj.dropdownOptions.deviationCategory}
          optionLabel="name"
          optionValue="name"
          value={rowData.smDeviationCategory ?? ""}
          hideLabel
          disabled
        />
      </div>
    );
  };

  const renderHeaderDeviation = () => {
    return (
      <div className="flex items-center">
        <Text label={i18n.t("label.cam.recommendation.table.deviation")} />
      </div>
    );
  };

  const renderDeviation = (rowData: any) => {
    return (
      <div className="h-24">
        {rowData?.smDeviationType === TYPE_OF_DEVIATION.TYPE_OF_DEVIATION_MANUAL ? (
          <AutoComplete
            variant={INPUT_STYLE_TYPE.PRIMARY}
            placeholder=""
            name={`deviation${rowData?.uid}`}
            value={rowData?.deviation?.name}
            suggestions={[`${rowData?.deviation?.name}`]}
            // completeMethod={(e: any) => searchManualDeviation(e, rowData)}
            itemTemplate={manualDeviationsTemplate}
            hideLabel
            disabled
          />
        ) : (
          <AutoComplete
            variant={INPUT_STYLE_TYPE.PRIMARY}
            placeholder=""
            name={`deviation${rowData?.uid}`}
            value={rowData?.deviation?.name}
            hideLabel
            disabled
          />
        )}
      </div>
    );
  };

  const renderHeaderDeviationLevel = () => {
    return (
      <div className="flex items-center">
        <Text label={i18n.t("label.cam.recommendation.table.deviationlevel")} />
      </div>
    );
  };

  const renderDeviationLevel = (rowData: any): ReactNode => {
    return (
      <div className="h-24">
        <SimpleDropDown
          name={`deviationLevel${rowData.uid}`}
          options={pageObj.dropdownOptions.deviationLevel}
          optionLabel="name"
          optionValue="name"
          value={rowData.maxApprovalLevel ?? ""}
          hideLabel
          disabled
        />
      </div>
    );
  };

  const renderHeaderActualDeviation = () => {
    return (
      <div className="flex items-center">
        <Text label={i18n.t("label.cam.recommendation.table.actualDeviation")} />
      </div>
    );
  };

  const renderActualDeviation = (rowData: any) => {
    return (
      <div className="h-24">
        <InputTextArea
          name={`actualDeviation${rowData?.uid}`}
          className="!h-full"
          variant={INPUT_STYLE_TYPE.SECONDARY}
          maxCount={1000}
          autoResize
          value={rowData?.actualDeviation}
          disabled
          hideLabel
        />
      </div>
    );
  };

  const renderHeaderMitigants = () => {
    return (
      <div className="flex items-center">
        <Text label={i18n.t("label.cam.recommendation.table.mitigants")} />
      </div>
    );
  };

  const renderMitigants = (rowData: any) => {
    return (
      <div className="h-24">
        <InputTextArea
          name={`mitigants${rowData?.uid}`}
          className="!h-full"
          variant={INPUT_STYLE_TYPE.SECONDARY}
          maxCount={1000}
          autoResize
          value={rowData?.mitigation}
          disabled
          hideLabel
        />
      </div>
    );
  };

  /*
    -------------------------------------------------------
    Main Rendering
    -------------------------------------------------------
  */
  return (
    <div className="grid gap-5 mb-5">
      <>
        <div className="border border-secondary-concrete rounded-xl p-5">
          <div className="flex flex-col gap-3">
            <div>
              {params.loanUid && data?.camStageUid ? (
                <DeviationTable
                  ref={tableRef}
                  standardDataFormatPromise={CamService.getCamDeviations}
                  search={`?loanUid=${params.loanUid}&stageUid=${data?.camStageUid}&reEvalDeviations=false`}
                  uniqueKey="uid"
                  mode={"UPDATE"}
                  isIgnoreInitialLoad
                >
                  <Column
                    header={renderHeaderTypeOfDeviation}
                    className="text-primary-ptext-1000 text-left text-xs h-12 pl-6"
                    style={{ minWidth: "170px", width: "170px" }}
                    body={renderTypeOfDeviations}
                  />

                  <Column
                    header={renderHeaderDeviationCategory}
                    className="pl-6 py-3 text-primary-ptext-1000 text-left text-xs"
                    style={{ minWidth: "170px", width: "170px" }}
                    body={renderDeviationCategory}
                  />

                  <Column
                    header={renderHeaderDeviation}
                    className="!w-64 pl-6 py-3 text-primary-ptext-1000 text-left text-xs"
                    body={renderDeviation}
                  />

                  <Column
                    header={renderHeaderDeviationLevel}
                    className="pl-6 py-3 text-primary-ptext-1000 text-left text-xs"
                    style={{ minWidth: "170px", width: "170px" }}
                    body={renderDeviationLevel}
                  />

                  <Column
                    header={renderHeaderActualDeviation}
                    className="pl-6 py-3 text-primary-ptext-1000 text-left text-xs"
                    body={renderActualDeviation}
                  />
                  <Column
                    header={renderHeaderMitigants}
                    className="px-6 py-3 text-primary-ptext-1000 text-left text-xs"
                    body={renderMitigants}
                  />
                </DeviationTable>
              ) : null}
            </div>
          </div>
        </div>
      </>

      <ErrorDialog
        onHide={() => {
          setShowDialogAPIError(false);
          setErrorMessage("");
        }}
        visible={showDialogAPIError}
        errorMessage={errorMessage}
      />
    </div>
  );
};

export default Deviations;
