import DynamicColumnTable from "components/DynamicColumnTable/DynamicColumnTable";
import ErrorDialog from "components/ErrorDialog/ErrorDialog";
import SkeletonCustom from "components/Skeleton/Skeleton";
import React, { forwardRef, useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import { formatDate, getAgeFromDob, getMonthFromYear } from "utils/CommonUtil";
import { APPLICANT_TYPE_LABEL, STAGE_OPTIONS } from "utils/Constant";
import { showCurrency } from "views/transaction/PageCAM/Helper";
import { ISummaryDropdownList } from "../../PageSummary";

export interface ISummaryLoanCustomerDetailsApplicantRowData {
  uid: string; // applicant uid
  applicantType: string;
  //input/ output
  fullName: string;
  smGender: string;
  dateOfBirth: string; // calculate age from this
  loan: {
    tenure: string; // calculate age of maturity from this
  };
  smRelationship: string;
  smEducation: string;
}

export interface ISummaryLoanCustomerDetailsEmploymentRowData {
  uid: string; // employment uid
  applicant: {
    uid: string;
    // smEducation: string;
  };
  smEmploymentNature: string;
  netIncomeMonth: string; // need to confirm  as well also have gross income
  expInYear: string;
}

export interface ISummaryLoanCustomerDetailsIncomeDetailsRowData {
  applicant: {
    uid: string;
    smEducation: string;
  };
  isIncomeConsidered: boolean | string;
}

interface ITableRow {
  FACTOR: string;
  [key: string]: number | null | string;
}
interface ITableColumn {
  label: string;
  key: keyof ITableRow;
}

interface IPageObj {
  rows: ITableRow[];
  columns: ITableColumn[];
  inputFields: string[] | [];
}
interface IProps {
  isLoading: boolean;
  data: {
    allApplicants: ISummaryLoanCustomerDetailsApplicantRowData[];
    allEmploymentDetails: ISummaryLoanCustomerDetailsEmploymentRowData[];
    allIncomeDetails: ISummaryLoanCustomerDetailsIncomeDetailsRowData[]; // source cam
  };
  dropdownOptions: ISummaryDropdownList;
}

export const initialPageObj: IPageObj = {
  columns: [],
  rows: [],
  inputFields: [],
};
const FIRST_COLUMN_KEY_DAY = "FACTOR";

const LoanCustomerDetails: React.FC<IProps> = forwardRef((props, ref) => {
  const { data, isLoading, dropdownOptions } = props;
  const params: { loanUid?: string; stageUid?: string } = useParams();

  /*
    -------------------------------------------------------
      State
    -------------------------------------------------------
    */
  const [pageObj, setPageObj] = useState<IPageObj>({
    ...initialPageObj,
  });
  const [errorMessage, setErrorMessage] = useState("");
  const [showDialogAPIError, setShowDialogAPIError] = useState(false);
  // ref

  // --------------------------- validator initialization ---------------------- //

  // --------------------------- global variables ---------------------- //

  /*
     -------------------------------------------------------
       useEffect
     -------------------------------------------------------
   */

  useEffect(() => {
    // structure the data which is accepted by component
    console.log("call api");
    const rows: ITableRow[] = [];
    const columns: ITableColumn[] = [];
   

    if (data?.allApplicants.length > 0 && !isLoading) {
      columns.push({ label: "", key: FIRST_COLUMN_KEY_DAY });

      // get all columns and rows
      let nameRow: ITableRow = {
        [FIRST_COLUMN_KEY_DAY]: "Name",
      };

      let genderRow: ITableRow = {
        [FIRST_COLUMN_KEY_DAY]: "Gender",
      };

      let ageRow: ITableRow = {
        [FIRST_COLUMN_KEY_DAY]: "Age (Years)",
      };

      let loanTenureRow: ITableRow = {
        [FIRST_COLUMN_KEY_DAY]: "Loan Tenure (Months)",
      };

      let ageAtMaturityRow: ITableRow = {
        [FIRST_COLUMN_KEY_DAY]: "Age at Maturity",
      };

      let relationshipRow: ITableRow = {
        [FIRST_COLUMN_KEY_DAY]: "Relationship with applicant",
      };

      let incomeConsideredRow: ITableRow = {
        [FIRST_COLUMN_KEY_DAY]: "Income Considered",
      };

      let employmentTypeRow: ITableRow = {
        [FIRST_COLUMN_KEY_DAY]: "Employment Type",
      };

      let monthlyIncomeRow: ITableRow = {
        [FIRST_COLUMN_KEY_DAY]: "Monthly Income",
      };

      let educationalQualificationsRow: ITableRow = {
        [FIRST_COLUMN_KEY_DAY]: "Educational Qualification",
      };

      let workExperienceRow: ITableRow = {
        [FIRST_COLUMN_KEY_DAY]: "Work Experience (years)",
      };

      for (const applicant of data.allApplicants) {
        let allApplicantEmployments: ISummaryLoanCustomerDetailsEmploymentRowData[] = [];
        const incomeDetails: ISummaryLoanCustomerDetailsIncomeDetailsRowData | undefined =
          data?.allIncomeDetails?.find((record) => record.applicant.uid === applicant.uid);
        if (data?.allEmploymentDetails.length > 0) {
          allApplicantEmployments = data?.allEmploymentDetails.filter(
            (record: ISummaryLoanCustomerDetailsEmploymentRowData) =>
              record.applicant.uid === applicant.uid
          );
        }

        if (allApplicantEmployments.length === 0) {
          //for column
          const key = `${applicant?.uid}`;
          const label =
            APPLICANT_TYPE_LABEL?.[applicant?.applicantType as keyof typeof APPLICANT_TYPE_LABEL] ??
            "";
          columns.push({ label: label, key: key });

          //for row
          nameRow = {
            ...nameRow,
            [applicant.uid]: applicant?.fullName ?? "-",
          };
          genderRow = {
            ...genderRow,
            [applicant.uid]:
              dropdownOptions?.gender?.find?.((record) => record.code === applicant?.smGender)
                ?.name ?? "-",
          };
          ageRow = {
            ...ageRow,
            [applicant.uid]: getAgeFromDob(applicant?.dateOfBirth) ?? "-", //need to get age from dob
          };
          loanTenureRow = {
            ...loanTenureRow,
            [applicant.uid]: applicant?.loan?.tenure ?? "-",
          };
          ageAtMaturityRow = {
            ...ageAtMaturityRow,
            [applicant.uid]:
              getAgeFromDob(applicant?.dateOfBirth) + getMonthFromYear(applicant?.loan?.tenure) ??
              "-", //age +loan tenure
          };
          relationshipRow = {
            ...relationshipRow,
            [applicant.uid]:
              dropdownOptions?.relationshipWithApplicant?.find?.(
                (record) => record.code === applicant?.smRelationship
              )?.name ?? "-",
          };
          incomeConsideredRow = {
            ...incomeConsideredRow,
            [applicant.uid]:
              incomeDetails?.isIncomeConsidered === true
                ? "Yes"
                : incomeDetails?.isIncomeConsidered === false
                ? "No"
                : "-",
          };
          employmentTypeRow = {
            ...employmentTypeRow,
            [applicant.uid]: "-",
          };
          monthlyIncomeRow = {
            ...monthlyIncomeRow,
            [applicant.uid]: "-",
          };
          educationalQualificationsRow = {
            ...educationalQualificationsRow,
            [applicant.uid]:
              dropdownOptions?.educationalQualifications?.find?.(
                (record) => record.code === applicant?.smEducation
              )?.name ?? "-",
          };
          workExperienceRow = {
            ...workExperienceRow,
            [applicant.uid]: "-",
          };
          continue;
        }

        for (const employment of allApplicantEmployments) {
          // for column
          const key = `${applicant?.uid}|${employment.uid}`; //composite key to find unique record
          const label =
            APPLICANT_TYPE_LABEL?.[applicant?.applicantType as keyof typeof APPLICANT_TYPE_LABEL] ??
            "";
          columns.push({ label: label, key: key });

          //for row
          if (employment.uid) {
            nameRow = {
              ...nameRow,
              [`${applicant.uid}|${employment.uid}`]: applicant?.fullName ?? "-",
            };
            genderRow = {
              ...genderRow,
              [`${applicant.uid}|${employment.uid}`]:
                dropdownOptions?.gender?.find?.((record) => record.code === applicant?.smGender)
                  ?.name ?? "-",
            };
            ageRow = {
              ...ageRow,
              [`${applicant.uid}|${employment.uid}`]: getAgeFromDob(applicant?.dateOfBirth) ?? "-",
            };
            loanTenureRow = {
              ...loanTenureRow,
              [`${applicant.uid}|${employment.uid}`]: applicant?.loan?.tenure ?? "-",
            };
            ageAtMaturityRow = {
              ...ageAtMaturityRow,
              [`${applicant.uid}|${employment.uid}`]:
                getAgeFromDob(applicant?.dateOfBirth) + getMonthFromYear(applicant?.loan?.tenure) ??
                "-", //age+loan tenure
            };
            relationshipRow = {
              ...relationshipRow,
              [`${applicant.uid}|${employment.uid}`]:
                dropdownOptions?.relationshipWithApplicant?.find?.(
                  (record) => record.code === applicant?.smRelationship
                )?.name ?? "-",
            };
            incomeConsideredRow = {
              ...incomeConsideredRow,
              [`${applicant.uid}|${employment.uid}`]:
                incomeDetails?.isIncomeConsidered === true
                  ? "Yes"
                  : incomeDetails?.isIncomeConsidered === false
                  ? "No"
                  : "-",
            };
            employmentTypeRow = {
              ...employmentTypeRow,
              [`${applicant.uid}|${employment.uid}`]:
                dropdownOptions?.employmentType?.find?.(
                  (record) => record.code === employment?.smEmploymentNature
                )?.name ?? "-",
            };
            monthlyIncomeRow = {
              ...monthlyIncomeRow,
              [`${applicant.uid}|${employment.uid}`]:
                showCurrency(employment?.netIncomeMonth) ?? "-",
            };
            educationalQualificationsRow = {
              ...educationalQualificationsRow,
              [`${applicant.uid}|${employment.uid}`]:
                dropdownOptions?.educationalQualifications?.find?.(
                  (record) => record.code === applicant?.smEducation
                )?.name ?? "-",
            };
            workExperienceRow = {
              ...workExperienceRow,
              [`${applicant.uid}|${employment.uid}`]: employment?.expInYear ?? "-",
            };
          }
        }
      }

      rows.push(nameRow);
      rows.push(genderRow);
      rows.push(ageRow);
      rows.push(loanTenureRow);
      rows.push(ageAtMaturityRow);
      rows.push(relationshipRow);
      rows.push(incomeConsideredRow);
      rows.push(employmentTypeRow);
      rows.push(monthlyIncomeRow);
      rows.push(educationalQualificationsRow);
      rows.push(workExperienceRow);

      // rows.push(workExperienceRow);

      setPageObj((prevPageObj) => ({
        ...prevPageObj,
        columns: columns,
        rows: rows,
      }));
    }
  }, [data, isLoading]);

  useEffect(() => {
    console.log("stage data", pageObj);
  }, [pageObj]);

  useEffect(() => {
    if (errorMessage) {
      setShowDialogAPIError(true);
    }
  }, [errorMessage]);
  // pageObj

  /*
    -------------------------------------------------------
      Service Calls
    -------------------------------------------------------
  */

  /*
    -------------------------------------------------------
      Handlers
    -------------------------------------------------------
  */

  /* 
    --------------------------------------------------------
    Validators:
    --------------------------------------------------------
  */

  /*
    -------------------------------------------------------
      Button Click Handlers
    -------------------------------------------------------
  */

  /*
    -------------------------------------------------------
      Helper Functions
    -------------------------------------------------------
  */

  /*
    -------------------------------------------------------
      Sectional Rendering
    -------------------------------------------------------
  */
  /*
    -------------------------------------------------------
    Main Rendering
    -------------------------------------------------------
  */
  // const { isLoading } = pageObj;
  return (
    <>
      <div className="">
        {isLoading ? (
          <SkeletonCustom height="19.375rem" width="100%" borderRadius="8px" animation="wave" />
        ) : (
          <DynamicColumnTable
            //  key={}
            rows={pageObj?.rows}
            columns={pageObj?.columns}
            inputFields={pageObj?.inputFields}
            // handleChange={handleChange}
          />
        )}
      </div>

      <ErrorDialog
        onHide={() => {
          setShowDialogAPIError(false);
          setErrorMessage("");
        }}
        visible={showDialogAPIError}
        errorMessage={errorMessage}
      />
    </>
  );
});

export default LoanCustomerDetails;
