import DynamicColumnTable from "components/DynamicColumnTable/DynamicColumnTable";
import ErrorDialog from "components/ErrorDialog/ErrorDialog";
import SkeletonCustom from "components/Skeleton/Skeleton";
import React, { forwardRef, useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import { formatDate } from "utils/CommonUtil";
import { STAGE_OPTIONS } from "utils/Constant";
// import {ran} from "typescript"

export interface IDataCapturedByData {
  //input/ output
  assignee: {
    name: string;
    parent: {
      name: string;
    };
    designation: {
      name: string;
    };
  };
  stageCompletionDate: string;
  stageName?: string;
}

interface ITableRow {
  DATA_CAPTURE: string;
  [key: string]: number | null | string;
}
interface ITableColumn {
  label: string;
  key: keyof ITableRow;
}

interface IPageObj {
  rows: ITableRow[];
  columns: ITableColumn[];
  inputFields: string[] | [];
}
interface IProps {
  isLoading: boolean;
  data: IDataCapturedByData[];
}

export const initialPageObj: IPageObj = {
  columns: [],
  rows: [],
  inputFields: [],
};
const FIRST_COLUMN_KEY_DAY = "DATA_CAPTURE";

const ALLOWS_MULTIPLE_STAGE_INITIATION = [
  STAGE_OPTIONS.PD.value,
  STAGE_OPTIONS.LEGAL_REPORT.value,
  STAGE_OPTIONS.RCU_REPORT.value,
  STAGE_OPTIONS.TECHNICAL_REPORT.value,
];

const DataCapturedBy: React.FC<IProps> = forwardRef((props, ref) => {
  const { data, isLoading } = props;
  const params: { loanUid?: string; stageUid?: string } = useParams();

  /*
    -------------------------------------------------------
      State
    -------------------------------------------------------
    */
  const [pageObj, setPageObj] = useState<IPageObj>({
    ...initialPageObj,
  });
  const [errorMessage, setErrorMessage] = useState("");
  const [showDialogAPIError, setShowDialogAPIError] = useState(false);
  // ref

  // --------------------------- validator initialization ---------------------- //

  // --------------------------- global variables ---------------------- //

  /*
     -------------------------------------------------------
       useEffect
     -------------------------------------------------------
   */

  useEffect(() => {
    // structure the data which is accepted by component
    console.log("call api");
    const rows: ITableRow[] = [];
    const columns: ITableColumn[] = [];
    // const totalColumns = data.length;
    // const inputFields: string[] = [];
    console.log("Data Capture ", data);

    if (data?.length > 0) {
      columns.push({ label: "Data Capture", key: FIRST_COLUMN_KEY_DAY });

      let dataCapturedByRow: ITableRow = {
        [FIRST_COLUMN_KEY_DAY]: "Data Captured By",
      };
      let designationRow: ITableRow = {
        [FIRST_COLUMN_KEY_DAY]: "Designation",
      };
      let managerRow: ITableRow = {
        [FIRST_COLUMN_KEY_DAY]: "Manager",
      };
      let dateOfEntryRow: ITableRow = {
        [FIRST_COLUMN_KEY_DAY]: "Date Of Entry",
      };

      for (const record of data) {
        // for column
        const key = `${record?.stageName}`;
        let suffixForLabel: string | number = "";
        if (ALLOWS_MULTIPLE_STAGE_INITIATION.includes(record?.stageName ?? "")) {
          suffixForLabel = columns.filter((column) => column.key === record?.stageName)?.length + 1;
        }
        const label = `${
          STAGE_OPTIONS[record?.stageName as keyof typeof STAGE_OPTIONS]?.label
        } ${suffixForLabel}`;
        columns.push({ label: label, key: key });

        //for row
        if (record?.stageName) {
          dataCapturedByRow = {
            ...dataCapturedByRow,
            [record.stageName]: record?.assignee?.name,
          };
          designationRow = {
            ...designationRow,
            [record.stageName]: record?.assignee?.designation?.name,
          };
          managerRow = {
            ...managerRow,
            [record.stageName]: record?.assignee?.parent?.name,
          };
          dateOfEntryRow = {
            ...dateOfEntryRow,
            [record.stageName]: formatDate(record?.stageCompletionDate),
          };
        }
      }

      rows.push(dataCapturedByRow);
      rows.push(designationRow);
      rows.push(managerRow);
      rows.push(dateOfEntryRow);

      setPageObj((prevPageObj) => ({
        ...prevPageObj,
        columns: columns,
        rows: rows,
      }));
    }
  }, [data]);


  useEffect(() => {
    if (errorMessage) {
      setShowDialogAPIError(true);
    }
  }, [errorMessage]);
  // pageObj

  /*
    -------------------------------------------------------
      Service Calls
    -------------------------------------------------------
  */

  /*
    -------------------------------------------------------
      Handlers
    -------------------------------------------------------
  */

  /* 
    --------------------------------------------------------
    Validators:
    --------------------------------------------------------
  */

  /*
    -------------------------------------------------------
      Button Click Handlers
    -------------------------------------------------------
  */

  /*
    -------------------------------------------------------
      Helper Functions
    -------------------------------------------------------
  */

  /*
    -------------------------------------------------------
      Sectional Rendering
    -------------------------------------------------------
  */
  /*
    -------------------------------------------------------
    Main Rendering
    -------------------------------------------------------
  */
  // const { isLoading } = pageObj;
  return (
    <>
      <div className="">
        {isLoading ? (
          <SkeletonCustom height="19.375rem" width="100%" borderRadius="8px" animation="wave" />
        ) : (
          <DynamicColumnTable
            //  key={}
            rows={pageObj?.rows}
            columns={pageObj?.columns}
            inputFields={pageObj?.inputFields}
            // handleChange={handleChange}
          />
        )}
      </div>

      <ErrorDialog
        onHide={() => {
          setShowDialogAPIError(false);
          setErrorMessage("");
        }}
        visible={showDialogAPIError}
        errorMessage={errorMessage}
      />
    </>
  );
});

export default DataCapturedBy;
