import AclGuard from "components/AclGuard/AclGuard";
import DatePicker from "components/DatePicker/DatePicker";
import { Column, EditableComponentRef } from "components/EditableTableV2/EditableTableV2";
import ErrorDialog from "components/ErrorDialog/ErrorDialog";
import IconButton, { BUTTON_TYPES } from "components/IconButton/IconButton";
import InputBox from "components/InputBox/InputBox";
import MenuBox from "components/MenuBox/MenuBox";
import SimpleDropDown from "components/SimpleDropDown/SimpleDropDown";
import ToastCustom from "components/Toast";
import i18n from "i18n";
import React, { useEffect, useRef, useState } from "react";
import { useParams } from "react-router-dom";
import CamService from "services/transaction/CamService";
import SimpleReactValidator from "simple-react-validator";
import { getStandardValidator, messageReplace } from "utils/CommonUtil";
import { INPUT_STYLE_TYPE, PAGE_MODE, TOAST_STYLE_TYPE } from "utils/Constant";
// import { updateCamBusinessProofRowDataObj } from "../Helper";
import MediaGallery, {
  IMediaInfo,
  initialMediaGallerySectionInfo,
} from "components/MediaGallery/MediaGallery";
import ReadOnlyTable from "components/ReadOnlyTable/ReadOnlyTable";

export interface ICamBusinessProofRowData {
  uid: string;
  loan: {
    uid: string;
  };
  stage: {
    uid: string;
  };
  documentProof?: string;
  docYearFrom: string;
  docYearTo: string;
  remarks: string;
  loanDocument: {
    uid?: string;
    docType?: {
      name?: string;
    };
    applicant?: {
      firstName?: string;
      middleName?: string;
      lastName?: string;
    };
    documentSections?: {
      fileName?: string;
      fileType?: string;
      loanDocument?: {
        docCategory?: string;
        docNo?: string;
      };
      sectionName?: string;
      url?: string;
    }[];
  };

  isActive?: boolean;
}

interface IPageObj {
  isLoading: {
    businessProof: boolean;
  };
  businessProof: ICamBusinessProofRowData[];
  pageMode: {
    businessProof: string;
  };
  // businessProofTableSearch:string;
}

const initialPageOj: IPageObj = {
  businessProof: [],
  pageMode: {
    businessProof: PAGE_MODE.VIEW,
  },
  isLoading: {
    businessProof: false,
  },
};

interface ISummaryBusinessProofData {
  stageUid: string;
}
interface IProps {
  data: ISummaryBusinessProofData;
}

const BusinessProof = (props: IProps) => {
  const { data } = props;
  /*
    -------------------------------------------------------
      State
    -------------------------------------------------------
  */
  const params: { loanUid?: string } = useParams();

  const [showDialogAPIError, setShowDialogAPIError] = useState(false);
  const [errorMessage, setErrorMessage] = useState<any>("");
  const [pageObj, setPageObj] = useState<IPageObj>({ ...initialPageOj });
  const [toastMessage, setToastMessage] = useState("");
  const [toastMessageVariant, setToastMessageVariant] = useState(TOAST_STYLE_TYPE.PRIMARY);
  const [triggerToast, setTriggerToast] = useState(false);
  const [showMediaGallery, setShowMediaGallery] = useState<boolean>(false);
  const [currentItemToViewInGallery, setCurrentItemToViewInGallery] = useState<IMediaInfo>({
    ...initialMediaGallerySectionInfo,
  });

  // ref
  const toastMessageRef = useRef(null);
  const tableRef = React.useRef<EditableComponentRef>(null);
  const actionMenuRef = useRef<any>(null);
  const [, forceUpdate] = useState<boolean>(false);

  const simpleValidator = useRef(
    new SimpleReactValidator({
      validators: { ...getStandardValidator() },
      ...messageReplace(),
      autoForceUpdate: { forceUpdate: () => forceUpdate((prev) => !prev) },
    })
  );

  // --------------------------- global variables ---------------------- //
  const actionMenuModel: any[] = [
    {
      items: [],
    },
  ];

  /*
       -------------------------------------------------------
         useEffect
       -------------------------------------------------------
     */

  useEffect(() => {
    if (errorMessage) {
      setShowDialogAPIError(true);
    }
  }, [errorMessage]);

  useEffect(() => {
    tableRef.current?.search();
  }, [data]);

  /*
      -------------------------------------------------------
        Service Calls
      -------------------------------------------------------
    */

  /*
      -------------------------------------------------------
        Handlers
      -------------------------------------------------------
    */

  /* 
      --------------------------------------------------------
      Validators
      --------------------------------------------------------
    */

  /*
      -------------------------------------------------------
        Button Click Handlers
      -------------------------------------------------------
    */

  const onViewFile = (e: any, rowData: ICamBusinessProofRowData) => {
    let preparedInfoInList: IMediaInfo = {
      uid: "",
      placeOfUpload: "",
      name: "",
      fileName: "",
      fileType: "",
      download: "",
      category: "",
      type: "",
      sectionName: "",
      url: "",
    };

    if (rowData?.loanDocument) {
      preparedInfoInList = {
        uid: rowData?.loanDocument?.uid ?? "",
        placeOfUpload: "",
        name: rowData?.loanDocument?.applicant?.firstName ?? "-",
        fileName: rowData?.loanDocument?.documentSections?.[0]?.fileName ?? "-",
        fileType: rowData?.loanDocument?.documentSections?.[0]?.fileType ?? "-",
        download: "",
        category: rowData?.loanDocument?.documentSections?.[0]?.loanDocument?.docCategory ?? "-",
        type: rowData?.loanDocument?.docType?.name ?? "-",
        sectionName: rowData?.loanDocument?.documentSections?.[0]?.sectionName ?? "-",
        url: rowData?.loanDocument?.documentSections?.[0]?.url ?? "",
      };
    }

    setCurrentItemToViewInGallery(preparedInfoInList);
    setShowMediaGallery(true);
  };

  /*
      -------------------------------------------------------
        Helper Functions
      -------------------------------------------------------
    */

  /*
      -------------------------------------------------------
        Sectional Renders
      -------------------------------------------------------
    */

  const renderBusinessProof = (rowData: ICamBusinessProofRowData) => {
    return (
      <div className="pt-4">
        <SimpleDropDown
          hideLabel
          placeholder="placeholder.legal.documentCopyType"
          name={`documentProof${rowData?.uid}`}
          value={rowData?.loanDocument?.docType?.name}
          variant={INPUT_STYLE_TYPE.PRIMARY}
          options={[`${rowData?.loanDocument?.docType?.name}`]}
          // optionLabel="name"
          // optionValue="code"
          disabled
          // onChange={(e) => handleChange(e, { rowData, field: "docCopyType" })}
          // loader={isLoading}
        />
      </div>
    );
  };

  const renderDocumentYearFrom = (rowData: ICamBusinessProofRowData) => {
    return (
      <div className="pt-4">
        <DatePicker
          hideLabel
          name={`docYearFrom${rowData.uid}`}
          maxDate={new Date()}
          showMinMaxRange
          value={rowData?.docYearFrom ? new Date(rowData?.docYearFrom) : ""}
          variant={INPUT_STYLE_TYPE.SECONDARY}
          disabled
          // disabled={pageMode === PAGE_MODE.VIEW}
        />
      </div>
    );
  };

  const renderDocumentYearTo = (rowData: ICamBusinessProofRowData) => {
    return (
      <div className="pt-4">
        <DatePicker
          hideLabel
          name={`docYearTo${rowData.uid}`}
          maxDate={new Date()}
          showMinMaxRange
          value={rowData?.docYearTo ? new Date(rowData?.docYearTo) : ""}
          variant={INPUT_STYLE_TYPE.SECONDARY}
          disabled
          // disabled={pageMode === PAGE_MODE.VIEW}
        />
      </div>
    );
  };

  const renderDocumentNumber = (rowData: ICamBusinessProofRowData) => {
    return (
      <div className="pt-4">
        <InputBox
          name={`remarks${rowData?.uid}`}
          hideLabel
          variant={INPUT_STYLE_TYPE.SECONDARY}
          value={rowData?.loanDocument?.documentSections?.[0]?.loanDocument?.docNo ?? ""}
          disabled
          // disabled={pageMode === PAGE_MODE.VIEW}
        />
      </div>
    );
  };

  const renderRemark = (rowData: ICamBusinessProofRowData) => {
    return (
      <div className="pt-4">
        <InputBox
          name={`remarks${rowData?.uid}`}
          hideLabel
          variant={INPUT_STYLE_TYPE.SECONDARY}
          value={rowData?.remarks}
          disabled
          // disabled={pageMode === PAGE_MODE.VIEW}
        />
      </div>
    );
  };

  const renderAction = (rowData: ICamBusinessProofRowData) => {
    return (
      <div className="flex gap-2 justify-center items-center">
        <div className="flex-shrink-0">
          <IconButton buttonType={BUTTON_TYPES.VIEW} onClick={(e: any) => onViewFile(e, rowData)} />
        </div>
        {/* <ButtonBox
          buttonType={BUTTON_TYPE.MENU}
          onClick={(e: any) => onClickActionMenu(e, rowData)}
        /> */}
      </div>
    );
  };

  /*
      -------------------------------------------------------
      Main Renders
      -------------------------------------------------------
    */
  return (
    <div className="grid gap-5">
      {/* <AclGuard override={{ read: pageObj?.pageMode?.businessProof === PAGE_MODE.VIEW }}> */}
      {params?.loanUid && data?.stageUid ? (
        <ReadOnlyTable
          ref={tableRef}
          uniqueKey="uid"
          standardDataFormatPromise={CamService.getIncomeDocumentList}
          search={`&isActive=${true}&loanUid=${params?.loanUid ?? ""}&stageUid=${
            data?.stageUid ?? ""
          }`}
          mode={"UPDATE"}
          variant="secondary"
          // isIgnoreInitialLoad
        >
          <Column
            header={i18n.t("label.cam.documentProof")}
            className="text-primary-ptext-1000 text-left text-xs h-12 pl-6"
            body={renderBusinessProof}
          />

          <Column
            header={i18n.t("label.cam.documentYearFrom")}
            className=" text-primary-ptext-1000 text-xs h-12 pl-6"
            body={renderDocumentYearFrom}
          />
          <Column
            header={i18n.t("label.cam.documentYearTo")}
            className=" text-primary-ptext-1000 text-xs h-12 pl-6"
            body={renderDocumentYearTo}
          />

          <Column
            header={i18n.t("label.summary.documentNo")}
            className=" text-primary-ptext-1000 text-xs h-12 pl-6"
            body={renderDocumentNumber}
          />
          <Column
            header={i18n.t("label.cam.Remark")}
            className=" text-primary-ptext-1000 text-xs h-12 pl-6"
            body={renderRemark}
          />

          <Column
            header={i18n.t("label.investmentInfo.table.colTitle.action")}
            className="text-primary-ptext-1000 text-right text-xs h-12 px-6"
            style={{ minWidth: "50px", width: "50px" }}
            body={renderAction}
          />
        </ReadOnlyTable>
      ) : null}
      {/* </AclGuard> */}
      <MediaGallery
        visible={showMediaGallery}
        onToggleVisibility={() => setShowMediaGallery((prev) => !prev)}
        list={[]}
        currentSection={currentItemToViewInGallery}
      />
      <MenuBox
        ref={actionMenuRef}
        model={actionMenuModel}
        variant="SECONDARY"
        buttonClassName="!bg-transparent"
        className="width-full-menubox bg-white  w-max p-1.5 rounded-md mt-0.5 border shadow-autoCompletePanel"
      />
      <ErrorDialog
        onHide={() => {
          setShowDialogAPIError(false);
          setErrorMessage("");
        }}
        visible={showDialogAPIError}
        errorMessage={errorMessage}
      />
    </div>
  );
};

export default BusinessProof;
