import AclGuard from "components/AclGuard";
import { Column, EditableComponentRef } from "components/EditableTableV2/EditableTableV2";
import ReadOnlyTable from "components/ReadOnlyTable/ReadOnlyTable";
import Section from "components/Section/Section";
import SimpleDropDown from "components/SimpleDropDown/SimpleDropDown";
import Text from "components/Text/Text";
import i18n from "i18n";
import React, { forwardRef, useEffect, useImperativeHandle, useRef, useState } from "react";
import { useParams } from "react-router-dom";
import { formatDate } from "utils/CommonUtil";
import {
  INPUT_STYLE_TYPE,
  PAGE_MODE,
  PROPERTY_ANNOTATION,
  PROPERTY_VALUATION_OPERATION_OPTIONS,
} from "utils/Constant";
// import { getNumber, showCurrency } from "../../Helper";
import SkeletonCustom from "components/Skeleton/Skeleton";
import { showCurrency } from "views/transaction/PageCAM/Helper";
import {
  ICamCollateralPropertyData,
  ICamPropertyValuationData,
} from "views/transaction/PageCAM/Sections/PropertyValuation/PropertyValuation";

interface IProps {
  isLoading: boolean;
  data?: ICamPropertyValuationData;
}

export interface ICamValuationRowData {
  uid: string; //stage uid
  partner: {
    name: string;
  };
  stageCompletionDate: string;
  technical: {
    govtTotalValuationOfProperty: number | string;
  };
}
export const initialCamPropertyValuationData: ICamPropertyValuationData = {
  collateralProperties: [],
  totalPropertyValuation: "",
  property: {
    uid: "",
    isCalculating: false,
  },
  // totalValuation: "",
};

const PropertyValuation: React.FC<IProps> = forwardRef((props, ref) => {
  const { data, isLoading } = props;
  // const params: { loanUid?: string } = useParams();
  /*
    -------------------------------------------------------
      State
    -------------------------------------------------------
    */
  const [pageObj, setPageObj] = useState<ICamPropertyValuationData>({
    ...initialCamPropertyValuationData,
  });
  // const [pageMode, setPageMode] = useState(PAGE_MODE.VIEW);
  const [toastMessage, setToastMessage] = useState("");
  const [triggerToast, setTriggerToast] = useState(false);
  const [errorMessage, setErrorMessage] = useState("");
  const [showDialogAPIError, setShowDialogAPIError] = useState(false);
  // ref
  const toastMessageRef = useRef(null);
  const tableRef = React.useRef<EditableComponentRef>(null);

  // --------------------------- validator initialization ---------------------- //
  const [, forceUpdate] = useState<boolean>(false);

  useImperativeHandle(ref, () => ({
    refresh: onSearch,
  }));

  // --------------------------- global variables ---------------------- //

  /*
     -------------------------------------------------------
       useEffect
     -------------------------------------------------------
   */

  useEffect(() => {
    setPageObj((prevObj) => ({
      ...prevObj,
      collateralProperties: data?.collateralProperties ?? [],
      totalPropertyValuation: data?.totalPropertyValuation ?? "",
    }));
    // when add the properties  add the  validations for the properties
    // resetAllSectionValidations(data?.collateralProperties?.length??0);
  }, [data]);

  /*
    -------------------------------------------------------
      Service Calls
    -------------------------------------------------------
  */

  /*
    -------------------------------------------------------
      Handlers
    -------------------------------------------------------
  */

  /* 
    --------------------------------------------------------
    Validators:
    --------------------------------------------------------
  */

  /*
    -------------------------------------------------------
      Button Click Handlers
    -------------------------------------------------------
  */

  /*
    -------------------------------------------------------
      Helper Functions
    -------------------------------------------------------
  */

  const onSearch = () => {
    tableRef.current?.search();
  };

  const getAllPropertyAnnotations = () => {
    let annotations = "";
    for (let i = 0; i < pageObj.collateralProperties.length; i++) {
      if (i === pageObj.collateralProperties.length - 1) {
        annotations += ` ${PROPERTY_ANNOTATION[i]}`;
      } else {
        annotations += `${PROPERTY_ANNOTATION[i]} + `;
      }
    }

    if (pageObj.collateralProperties.length > 0) {
      return `( ${annotations} )`;
    }
    return "";
  };

  // const getAllPropertiesTotalValuation = () => {
  //   let totalValuation = 0;
  //   pageObj.properties.forEach((property, index) => {
  //     if (property.calculatedValuation === "") {
  //       totalValuation += 0;
  //       return;
  //     }
  //     totalValuation += property.calculatedValuation;
  //   });
  //   return ` ${totalValuation ? " " + numberFormatter(totalValuation, "en-IN", 0) : ""}`;
  // };

  /*
    -------------------------------------------------------
      Sectional Rendering
    -------------------------------------------------------
  */

  const renderValuationMethod = (property: ICamCollateralPropertyData, index: number) => {
    // if (property?.valuationTable?.length === 0) {
    //   return undefined;
    // }

    return (
      <div className="flex ">
        <div className="w-30 mr-3">
          <SimpleDropDown
            hideLabel
            // placeholder="placeholder.cam.typeOfApplicant"
            name={`smValuationOperationType${index}`}
            value={property?.propertyValuation?.smValuationOperationType ?? ""}
            variant={INPUT_STYLE_TYPE.PRIMARY}
            options={PROPERTY_VALUATION_OPERATION_OPTIONS}
            optionLabel="name"
            optionValue="code"
            disabled
          />
        </div>
        <Text className=" text-xs font-medium " label={`(${PROPERTY_ANNOTATION[index]})`} />
      </div>
    );
  };

  const renderCalculatedValuation = (property: ICamCollateralPropertyData) => {

    return (
      <Text
        className="text-right"
        label={`${
          property.propertyValuation?.finalValuation
            ? " " + showCurrency(property.propertyValuation?.finalValuation)
            : ""
        }`}
      />
    ); 
  };

  const renderVenderName = (rowData: ICamValuationRowData) => {
    return (
      <Text className="  text-primary-ptext-1000 " label={`${rowData?.partner?.name ?? ""}`} />
    );
  };

  const renderDateOfValuation = (rowData: ICamValuationRowData) => {
    return (
      <Text
        className="  text-primary-ptext-1000 "
        label={`${formatDate(rowData?.stageCompletionDate)}`}
      />
    );
  };

  const renderTotalValuation = (rowData: ICamValuationRowData) => {
    return (
      <Text
        className="  text-primary-ptext-1000 text-right"
        label={`${showCurrency(rowData?.technical?.govtTotalValuationOfProperty)}`}
      />
    );
  };

  /*
    -------------------------------------------------------
    Main Rendering
    -------------------------------------------------------
  */

  return (
    <>
      {isLoading ? (
        <SkeletonCustom height="19.375rem" width="100%" borderRadius="8px" animation="wave" />
      ) : (
        <>
          {pageObj.collateralProperties.map(
            (property: ICamCollateralPropertyData, index: number) => (
              <div key={index}>
                <div className=" border border-primary-ptext-500 rounded-xl p-5 flex flex-col gap-3">
                  <AclGuard override={{ read: property.pageMode === PAGE_MODE.VIEW }}>
                    <div className="grid grid-cols-4 gap-5">
                      <div className="flex flex-col">
                        <Text
                          className="text-primary-gray-600 text-xs"
                          label={"label.dde.propertyAddress"}
                        />
                        <Text
                          className="text-primary-ptext-1000 text-xs "
                          label={property?.address ?? ""}
                        />
                      </div>
                      <div className="flex flex-col">
                        <Text className="text-primary-gray-600 text-xs" label={"heading.pincode"} />
                        <Text
                          className="text-primary-ptext-1000 text-xs"
                          label={property?.pincode?.pinCode ?? ""}
                        />
                      </div>
                      <div className="flex flex-col">
                        <Text
                          className="text-primary-gray-600 text-xs"
                          label={"input.pincode.city"}
                        />
                        <Text
                          className="text-primary-ptext-1000 text-xs"
                          label={property?.city?.name ?? ""}
                        />
                      </div>
                      <div className="flex flex-col">
                        <Text
                          className="text-primary-gray-600 text-xs"
                          label={"input.pincode.state"}
                        />
                        <Text
                          className="text-primary-ptext-1000 text-xs"
                          label={property?.city?.state?.name ?? ""}
                        />
                      </div>
                      <div className="col-span-4">
                        <div className="mb-3">
                          <Text
                            className="text-primary-ptext-1000  text-xs "
                            label={"label.cam.valuation"}
                          />
                        </div>
                        <ReadOnlyTable
                          value={property?.valuationTable}
                          className={` ${
                            property?.valuationTable?.length
                              ? "  "
                              : " rcu-stage-document-verification"
                          }`}
                          ref={tableRef}
                          dataKey="uid"
                        >
                          <Column
                            className="text-primary-ptext-1000 text-left text-xs  p-1 h-12 pl-6"
                            header={i18n.t("label.cam.vendorName")}
                            body={renderVenderName}
                            footer={property?.valuationTable?.length != 0 ?() => renderValuationMethod(property, index):undefined}
                          />
                          <Column
                            className="text-primary-ptext-1000 text-left text-xs  p-1 h-12 pl-6"
                            header={i18n.t("label.cam.dateOfValuation")}
                            body={renderDateOfValuation}
                          />
                          <Column
                            className="text-primary-ptext-1000 text-right text-xs  p-1 h-12 pl-6 pr-5"
                            header={i18n.t("label.cam.totalValuation()")}
                            body={renderTotalValuation}
                            style={{ maxWidth: "168px" }}
                            footer={property?.valuationTable?.length != 0 ?() => renderCalculatedValuation(property):undefined}
                          />
                        </ReadOnlyTable>
                      </div>
                    </div>
                  </AclGuard>
                </div>
              </div>
            )
          )}
          {pageObj?.collateralProperties.length > 0 ? (
            <div className="bg-[#fbfbff] flex justify-between pl-7 pr-11 py-6">
              <div>
                <Text
                  className=" font-medium text-xs"
                  label={"label.cam.totalValuationBeingConsidered"}
                  args={{ properties: getAllPropertyAnnotations() }}
                />
              </div>
              <div>
                <Text
                  className=" font-medium text-xs"
                  label={
                    pageObj?.totalPropertyValuation
                      ? " " + showCurrency(pageObj?.totalPropertyValuation)
                      : ""
                  }
                />
              </div>
            </div>
          ) : null}
        </>
      )}
    </>
  );
});

export default PropertyValuation;
