import AclGuard from "components/AclGuard/AclGuard";
import ButtonBox from "components/ButtonBox/ButtonBox";
import EditableTableV2, { Column, EditableComponentRef } from "components/EditableTableV2";
import ErrorDialog from "components/ErrorDialog/ErrorDialog";
import IconButton, { BUTTON_TYPES } from "components/IconButton/IconButton";
import InputBox from "components/InputBox/InputBox";
import InputTextArea from "components/InputTextArea/InputTextArea";
import ReadOnlyTable from "components/ReadOnlyTable/ReadOnlyTable";
import SaveComponent from "components/Save/SaveComponent";
import SimpleDropDown from "components/SimpleDropDown";
import Text from "components/Text/Text";
import ToastCustom from "components/Toast";
import i18n from "i18n";
import { DropdownChangeParams } from "primereact/dropdown";
import React, { BaseSyntheticEvent, ReactNode, useEffect, useRef, useState } from "react";
import { useParams } from "react-router-dom";
import StaticMasterDataService from "services/masterdata/StaticMaster/StaticMasterDataService";
import CamService from "services/transaction/CamService";
import SimpleReactValidator from "simple-react-validator";
import {
  getStandardValidator,
  messageReplace,
  parseStandardAPIErrorMessage,
  validateGetAPIResponse,
  validatePutAPIResponse,
} from "utils/CommonUtil";
import {
  BUTTON_TYPE,
  INPUT_STYLE_TYPE,
  PAGE_MODE,
  STATIC_MASTER_TYPE_TOAST_TEXT,
  TOAST_STYLE_TYPE,
} from "utils/Constant";

interface IDropDownOptions {
  name: string;
  code: string;
  uid: string;
}

interface IDropdownList {
  sanctionCategory: IDropDownOptions[];
  sanctionType: IDropDownOptions[];
}

interface IPageObj {
  dropdownOptions: IDropdownList;
  stageUid: string;
  tableObj: {
    search: string;
  };
}

interface ISanctionConditionTableRowData {
  uid: string;
  sanctionCondition: string;
  smSanctionType: string;
  smCategory: string;
  remarks: string;
  isActive: true;
}

const initialSanctionConditionTableRowData: ISanctionConditionTableRowData = {
  uid: "",
  sanctionCondition: "",
  smSanctionType: "",
  smCategory: "",
  remarks: "",
  isActive: true,
};

const initialPageOj: IPageObj = {
  dropdownOptions: {
    sanctionCategory: [],
    sanctionType: [],
  },
  stageUid: "",
  tableObj: {
    search: ``,
  },
};

interface IProps {
  data: {
    camStageUid: string;
  };
}

const SanctionConditions = (props: IProps) => {
  const { data } = props;
  const params: { loanUid?: string } = useParams();

  /*
    -------------------------------------------------------
      State
    -------------------------------------------------------
  */

  const [pageObj, setPageObj] = useState<IPageObj>({ ...initialPageOj });

  const [, forceUpdate] = useState<boolean>(false);
  const [errorMessage, setErrorMessage] = useState<any>("");
  const [showDialogAPIError, setShowDialogAPIError] = useState(false);

  // ref
  const sanctionConditionsTableRef = useRef<EditableComponentRef>(null);

  // --------------------------- global variables ---------------------- //

  /*
     -------------------------------------------------------
       useEffect
     -------------------------------------------------------
   */

  useEffect(() => {
    getSanctionTypeOptions();
    getSanctionCategory();
  }, []);

  useEffect(() => {
    if (
      sanctionConditionsTableRef &&
      sanctionConditionsTableRef.current &&
      pageObj.tableObj.search
    ) {
      sanctionConditionsTableRef.current?.search();
    }
  }, [pageObj.tableObj.search]);

  useEffect(() => {
    if (errorMessage) {
      setShowDialogAPIError(true);
    }
  }, [errorMessage]);

  /*
    -------------------------------------------------------
      Service Calls
    -------------------------------------------------------
  */

  const getSanctionTypeOptions = async () => {
    StaticMasterDataService.getAll(
      0,
      30,
      "staticMasterType.code=SANCTION_CONDITION_TYPE&&"
    )
      .then((res) => {
        if (validateGetAPIResponse(res)) {
          updateDropdownOptions("sanctionType", res?.data?.results);
        }
      })
      .catch((err) => {
        console.log(err);
        setErrorMessage(parseStandardAPIErrorMessage(err));
      });
  };

  const getSanctionCategory = async () => {
    StaticMasterDataService.getAll(
      0,
      10,
      "staticMasterType.code=SANCTION_CONDITION_CATEGORY&&"
    )
      .then((res) => {
        if (validateGetAPIResponse(res)) {
          updateDropdownOptions("sanctionCategory", res?.data?.results);
        }
      })
      .catch((err) => {
        console.log(err);
        setErrorMessage(parseStandardAPIErrorMessage(err));
      });
  };

  /*
    -------------------------------------------------------
      Handlers
    -------------------------------------------------------
  */
  /* 
    --------------------------------------------------------
    Validators:
    --------------------------------------------------------
  */

  /*
    -------------------------------------------------------
      Button Click Handlers
    -------------------------------------------------------
  */

  /*
    -------------------------------------------------------
      Helper Functions
    -------------------------------------------------------
  */

  const enrichForSave = () => {
    const prepareRecords = () => {
      const sanctionConditionsRecords = [
        ...(sanctionConditionsTableRef.current?.getAddedRecords() ?? []),
        ...(sanctionConditionsTableRef.current?.getUpdatedRecords() ?? []),
        ...(sanctionConditionsTableRef.current?.getDeletedRecords() ?? []),
      ].map((record) => ({
        uid: record?.uid ? record?.uid : null,
        sanctionCondition: record?.sanctionCondition ?? "",
        smSanctionType: record?.smSanctionType ?? "",
        smCategory: record?.smCategory ?? "",
        remarks: record?.remarks ?? "",
        isActive: record?.isActive ?? false,
      }));

      return sanctionConditionsRecords;
    };

    const payloadData = {
      loan: {
        uid: params?.loanUid ?? null,
      },
      stage: {
        uid: pageObj.stageUid,
      },
      sanctionedConditions: prepareRecords(),
    };

    return payloadData;
  };

  const updateDropdownOptions = (key: keyof IDropdownList, options: any[]) => {
    setPageObj((prev: IPageObj) => ({
      ...prev,
      dropdownOptions: {
        ...prev?.dropdownOptions,
        [key]: options ?? [],
      },
    }));
  };

  /*
    -------------------------------------------------------
      Sectional Rendering
    -------------------------------------------------------
  */

  const renderSanctionConditionHeader = () => {
    return (
      <div className="flex items-center">
        <Text label={i18n.t("label.cam.recommendation.table.sancCondition")} />
      </div>
    );
  };

  const renderSanctionCondition = (rowData: any): ReactNode => {
    return (
      <div className="h-24">
        <InputTextArea
          name={`sanctionCondition${rowData?.uid}`}
          className="!h-full"
          variant={INPUT_STYLE_TYPE.SECONDARY}
          maxCount={200}
          autoResize
          value={rowData?.sanctionCondition}
          disabled
          hideLabel
        />
      </div>
    );
  };

  const renderHeaderSanctionType = () => {
    return (
      <div className="flex items-center">
        <Text label={i18n.t("label.cam.recommendation.table.type")} />
      </div>
    );
  };

  const renderSanctionType = (rowData: any): ReactNode => {
    return (
      <div className="h-24">
        <SimpleDropDown
          name={`smSanctionType${rowData?.uid}`}
          className="!w-30"
          options={pageObj.dropdownOptions.sanctionType}
          optionLabel="name"
          optionValue="code"
          value={rowData?.smSanctionType ?? ""}
          disabled
          hideLabel
        />
      </div>
    );
  };

  const renderHeaderSanctionCategory = () => {
    return (
      <div className="flex items-center">
        <Text label={i18n.t("label.cam.recommendation.table.category")} />
      </div>
    );
  };

  const renderSanctionCategory = (rowData: any): ReactNode => {
    return (
      <div className="h-24">
        <SimpleDropDown
          name={`smCategory${rowData?.uid}`}
          className="!w-30"
          options={pageObj.dropdownOptions.sanctionCategory}
          optionLabel="name"
          optionValue="code"
          value={rowData?.smCategory ?? ""}
          disabled
          hideLabel
        />
      </div>
    );
  };

  const renderHeaderRemarks = () => {
    return (
      <div className="flex items-center">
        <Text label={i18n.t("label.cam.recommendation.table.remark")} />
      </div>
    );
  };

  const renderSanctionConditionRemarks = (rowData: any): ReactNode => {
    return (
      <div className="h-24">
        <InputBox
          name={`remarks${rowData?.uid}`}
          className="!h-6"
          variant={INPUT_STYLE_TYPE.SECONDARY}
          value={rowData?.remarks}
          disabled
          hideLabel
        />
      </div>
    );
  };

  const renderHeaderAction = () => {
    return (
      <div className="flex items-center">
        <Text label={i18n.t("label.cam.recommendation.table.action")} />
      </div>
    );
  };


  /*
    -------------------------------------------------------
    Main Rendering
    -------------------------------------------------------
  */
  return (
    <React.Fragment>
      <div className="grid gap-5 mb-5">
        <div className="border border-secondary-concrete rounded-xl p-5">
          <div className="flex flex-col gap-3">
            {params?.loanUid && data?.camStageUid && (
              <div>
                <ReadOnlyTable
                  ref={sanctionConditionsTableRef}
                  standardDataFormatPromise={CamService.getSanctionConditions}
                  search={`&loan.uid=${params.loanUid}&stage.uid=${data?.camStageUid}`}
                  uniqueKey="uid"
                  paginator
                >
                  <Column
                    header={renderSanctionConditionHeader}
                    className="!w-64 pl-6 py-3 text-primary-ptext-1000 text-left text-xs"
                    body={renderSanctionCondition}
                  />

                  <Column
                    header={renderHeaderSanctionType}
                    className="!w-30 pl-6 py-3 text-primary-ptext-1000 text-left text-xs"
                    body={renderSanctionType}
                  />

                  <Column
                    header={renderHeaderSanctionCategory}
                    className="!w-30 pl-6 py-3 text-primary-ptext-1000 text-left text-xs"
                    body={renderSanctionCategory}
                  />

                  <Column
                    header={renderHeaderRemarks}
                    className="!w-64 px-6 py-3 text-primary-ptext-1000 text-left text-xs"
                    body={renderSanctionConditionRemarks}
                  />
                </ReadOnlyTable>
              </div>
            )}
          </div>
        </div>


        <ErrorDialog
          onHide={() => {
            setShowDialogAPIError(false);
            setErrorMessage("");
          }}
          visible={showDialogAPIError}
          errorMessage={errorMessage}
        />
      </div>
    </React.Fragment>
  );
};

export default SanctionConditions;
