import EditableTableV2, {
  Column,
  EditableComponentRef,
} from "components/EditableTableV2/EditableTableV2";
import InputBox from "components/InputBox";
import ReadOnlyTable from "components/ReadOnlyTable/ReadOnlyTable";
import SimpleDropDown from "components/SimpleDropDown/SimpleDropDown";
import SkeletonCustom from "components/Skeleton/Skeleton";
import Text from "components/Text/Text";
import i18n from "i18n";
import React, { forwardRef, useEffect, useRef, useState } from "react";
import { useParams } from "react-router-dom";
import CamService from "services/transaction/CamService";
import SimpleReactValidator from "simple-react-validator";
import { getStandardValidator, messageReplace } from "utils/CommonUtil";
import {
  CAM_APPLICANT_TYPE_OPTIONS,
  INPUT_STYLE_TYPE,
  YesOrNoOptionsWithBooleanValue,
} from "utils/Constant";
import { showCurrency } from "views/transaction/PageCAM/Helper";
import {
  ICamIncomeDetailsPageObj,
  ICamIncomeDetailsRowData,
} from "views/transaction/PageCAM/Sections/IncomeDetails/IncomeDetails";

export interface ISummaryIncomeDetailsPageObj extends ICamIncomeDetailsPageObj {
  camStageUid: string;
}
interface IProps {
  data: ISummaryIncomeDetailsPageObj;
  isLoading: boolean;
}

export const initialSummaryIncomeDetailsData: ISummaryIncomeDetailsPageObj = {
  camStageUid: "",
  total: {
    appraisedNetMonthlyIncome: 0,
    monthlyObligation: 0,
  },
  roi: 0,
  tenure: 0,
  // customerNameOptions: [],
  row: {
    isCalculating: false,
    uid: "",
  },
};

const IncomeDetails: React.FC<IProps> = forwardRef((props, ref) => {
  const { data, isLoading } = props;
  const params: { loanUid?: string } = useParams();
  /*
    -------------------------------------------------------
      State
    -------------------------------------------------------
    */
  const [pageObj, setPageObj] = useState<ISummaryIncomeDetailsPageObj>({
    ...initialSummaryIncomeDetailsData,
  });

  // ref

  const tableRef = React.useRef<EditableComponentRef>(null);

  // --------------------------- validator initialization ---------------------- //
  const [, forceUpdate] = useState<boolean>(false);

  const simpleValidator = useRef(
    new SimpleReactValidator({
      validators: { ...getStandardValidator() },
      ...messageReplace(),
      autoForceUpdate: { forceUpdate: () => forceUpdate((prev) => !prev) },
    })
  );

  // --------------------------- global variables ---------------------- //

  /*
     -------------------------------------------------------
       useEffect
     -------------------------------------------------------
   */

  useEffect(() => {
    // if (data?.total) {
    setPageObj((prevObj) => ({
      ...prevObj,
      total: { ...data.total },
      camStageUid: data?.camStageUid,
    }));
    // when add the properties  add the  validations for the properties
    // resetAllSectionValidations(data?.properties?.length);
    // }
  }, [data]);

  /*
    -------------------------------------------------------
      Service Calls
    -------------------------------------------------------
  */

  /*
    -------------------------------------------------------
      Handlers
    -------------------------------------------------------
  */

  const handleChange = (e: any) => {
    setPageObj((prevObj) => ({
      ...prevObj,
      [e.target.name]: e.target.value,
    }));
  };

  const handleApplicantTypeChange = (e: any, rowData: ICamIncomeDetailsRowData) => {
    let rowDataObj: ICamIncomeDetailsRowData = { ...rowData };
    rowDataObj = {
      ...rowDataObj,
      applicant: {
        ...rowDataObj.applicant,
        applicantType: e.target.value,
        uid: "",
      },
      // monthlyObligation: "",
      // maxEmi: "",
      // emiFactor: "",
      // eligibility: "",
      // foir: "",
    };

    tableRef.current?.reportDataChange(rowDataObj);
  };

  const handleCustomerNameChange = (e: any, rowData: ICamIncomeDetailsRowData) => {
    let rowDataObj: ICamIncomeDetailsRowData = { ...rowData };
    rowDataObj = {
      ...rowDataObj,
      applicant: {
        ...rowDataObj.applicant,
        uid: e.target.value,
      },
      // monthlyObligation: "",
      // maxEmi: "",
      // emiFactor: "",
      // eligibility: "",
      // foir: "",
    };

    tableRef.current?.reportDataChange(rowDataObj);
  };

  /* 
    --------------------------------------------------------
    Validators:
    --------------------------------------------------------
  */

  /*
    -------------------------------------------------------
      Button Click Handlers
    -------------------------------------------------------
  */

  /*
    -------------------------------------------------------
      Helper Functions
    -------------------------------------------------------
  */

  /*
    -------------------------------------------------------
      Sectional Rendering
    -------------------------------------------------------
  */
  const renderApplicantType = (rowData: ICamIncomeDetailsRowData) => {
    return (
      <div className="pt-4">
        <SimpleDropDown
          hideLabel
          placeholder="placeholder.cam.applicantType"
          name={`applicantType${rowData?.uid}`}
          value={rowData?.applicant?.applicantType}
          variant={INPUT_STYLE_TYPE.PRIMARY}
          options={CAM_APPLICANT_TYPE_OPTIONS}
          optionLabel="name"
          optionValue="code"
          disabled
        />
      </div>
    );
  };

  const renderCustomerName = (rowData: ICamIncomeDetailsRowData) => {
    return (
      <div className="pt-4">
        <SimpleDropDown
          hideLabel
          placeholder="placeholder.cam.customerName"
          name={`customerName${rowData?.uid}`}
          value={rowData?.applicant?.fullName}
          variant={INPUT_STYLE_TYPE.PRIMARY}
          options={[`${rowData?.applicant?.fullName}`]}
          disabled
        />
      </div>
    );
  };

  const renderIncomeScheme = (rowData: ICamIncomeDetailsRowData) => {
    return (
      <div className="pt-4 w-84 pr-3">
        <SimpleDropDown
          hideLabel
          placeholder="placeholder.cam.incomeScheme"
          className="truncate"
          value={rowData?.incomeScheme?.name}
          variant={INPUT_STYLE_TYPE.PRIMARY}
          options={[`${rowData?.incomeScheme?.name}`]}
          disabled
        />
      </div>
    );
  };

  const renderAppraisedNetMonthlyIncome = (rowData: ICamIncomeDetailsRowData) => {
    return (
      <div className="pt-4">
        <InputBox
          name={`appraisedNetMonthlyIncome${rowData?.uid}`}
          variant={INPUT_STYLE_TYPE.SECONDARY}
          className="text-right"
          value={showCurrency(rowData?.appraisedNetMonthlyIncome)}
          hideLabel
          disabled
        />
      </div>
    );
  };

  const renderIsIncomeSchemeConsidered = (rowData: ICamIncomeDetailsRowData) => {
    return (
      <div className="pt-4">
        <SimpleDropDown
          hideLabel
          placeholder="placeholder.cam.incomeScheme"
          name={`isIncomeConsidered${rowData?.uid}`}
          value={rowData?.isIncomeConsidered}
          variant={INPUT_STYLE_TYPE.PRIMARY}
          options={YesOrNoOptionsWithBooleanValue}
          optionLabel="name"
          optionValue="code"
          disabled
        />
      </div>
    );
  };

  const renderMonthlyObligation = (rowData: ICamIncomeDetailsRowData) => {
    return (
      <div className="pt-4">
        <InputBox
          name={`monthlyObligation${rowData?.uid}`}
          variant={INPUT_STYLE_TYPE.SECONDARY}
          value={rowData?.monthlyObligation}
          className="text-right "
          disabled
          hideLabel
        />
      </div>
    );
  };

  const renderFoir = (rowData: ICamIncomeDetailsRowData) => {
    return (
      <div className="pt-4">
        <InputBox
          name={`foir${rowData?.uid}`}
          variant={INPUT_STYLE_TYPE.SECONDARY}
          value={rowData?.foir}
          disabled
          className="text-right "
          hideLabel
        />
      </div>
    );
  };

  const renderMaxEmi = (rowData: ICamIncomeDetailsRowData) => {
    return (
      <div className="pt-4">
        <InputBox
          name={`maxEmi${rowData?.uid}`}
          variant={INPUT_STYLE_TYPE.SECONDARY}
          value={rowData?.maxEmi}
          className="text-right"
          disabled
          hideLabel
        />
      </div>
    );
  };

  const renderEmiFactor = (rowData: ICamIncomeDetailsRowData) => {
    return (
      <div className="pt-4">
        <InputBox
          variant={INPUT_STYLE_TYPE.SECONDARY}
          value={rowData?.emiFactor}
          className="text-right"
          disabled
          hideLabel
          // hideErrorRow
        />
      </div>
    );
  };

  const renderEligibility = (rowData: ICamIncomeDetailsRowData) => {
    return (
      <div className="pt-4">
        <InputBox
          name={`eligibility${rowData?.uid}`}
          variant={INPUT_STYLE_TYPE.SECONDARY}
          value={rowData?.eligibility}
          className="text-right"
          disabled
          hideLabel
        />
      </div>
    );
  };

  const renderTotal = () => {
    return (
      <Text
        className=" font-medium text-primary-ptext-1000  "
        label={`label.cibilDetails.enquirySummary.thead.total`}
      />
    );
  };

  const renderTotalOfNetAppraisedMonthlyIncome = () => {
    const total = showCurrency(pageObj?.total?.appraisedNetMonthlyIncome);
    return (
      <Text
        className=" font-medium text-primary-ptext-1000 text-right"
        label={pageObj?.total?.appraisedNetMonthlyIncome > 0 ? `${total}` : ""}
      />
    );
  };

  const renderTotalOfMonthlyObligation = () => {
    const total = showCurrency(pageObj?.total?.monthlyObligation);
    return (
      <Text
        className=" font-medium text-primary-ptext-1000 text-right"
        label={pageObj?.total?.monthlyObligation > 0 ? `${total}` : ""}
      />
    );
  };
  /*
    -------------------------------------------------------
    Main Rendering
    -------------------------------------------------------
  */
  return (
    <div>
      <div className=" border border-primary-ptext-500 rounded-xl p-5 flex flex-col gap-3">
        {isLoading ? (
          <SkeletonCustom height="19.375rem" width="100%" borderRadius="8px" animation="wave" />
        ) : (
          <div className="col-span-4">
            {params?.loanUid && pageObj?.camStageUid ? (
              <ReadOnlyTable
                ref={tableRef}
                uniqueKey="uid"
                standardDataFormatPromise={CamService.getIncomeDetails}
                search={`&loan.uid=${params?.loanUid}&isActive=true&stage.uid=${pageObj?.camStageUid}`}
              >
                <Column
                  header={i18n.t("label.cam.applicantType")}
                  className="text-primary-ptext-1000 text-left text-xs h-12 pl-6 min-w-35"
                  body={renderApplicantType}
                  footer={renderTotal}
                />
                <Column
                  header={i18n.t("label.cam.customerName")}
                  className="text-primary-ptext-1000 text-left text-xs h-12 pl-6 min-w-50"
                  body={renderCustomerName}
                />
                <Column
                  header={i18n.t("label.cam.incomeScheme")}
                  className=" text-primary-ptext-1000 text-xs h-12 pl-6 max-w-84 w-84 "
                  body={renderIncomeScheme}
                />
                <Column
                  header={i18n.t("label.cam.appraisedNetMonthlyIncome")}
                  className="table-header-right text-primary-ptext-1000 text-xs h-12 pl-6"
                  body={renderAppraisedNetMonthlyIncome}
                  footer={renderTotalOfNetAppraisedMonthlyIncome}
                  footerClassName=" text-right"
                />
                <Column
                  header={i18n.t("label.cam.isIncomeConsidered")}
                  className="table-header-right text-primary-ptext-1000 text-xs h-12 pl-6"
                  body={renderIsIncomeSchemeConsidered}
                />
                <Column
                  header={i18n.t("label.cam.monthlyObligation")}
                  className="table-header-right text-primary-ptext-1000 text-xs h-12 pl-6"
                  body={renderMonthlyObligation}
                  footer={renderTotalOfMonthlyObligation}
                  footerClassName=" text-right"
                />
                <Column
                  header={i18n.t("label.cam.foir(AsPerPolicy)")}
                  className="table-header-right text-primary-ptext-1000 text-xs h-12 pl-6"
                  body={renderFoir}
                />
                <Column
                  header={i18n.t("label.cam.maxEmi")}
                  className="table-header-right text-primary-ptext-1000 text-xs h-12 pl-6"
                  body={renderMaxEmi}
                />
                <Column
                  header={i18n.t("label.cam.emiFactor")}
                  className="table-header-right text-primary-ptext-1000 text-xs h-12 pl-6"
                  body={renderEmiFactor}
                />
                <Column
                  header={i18n.t("label.cam.eligibility")}
                  className="table-header-right text-primary-ptext-1000 text-xs h-12 pl-6"
                  body={renderEligibility}
                />
              </ReadOnlyTable>
            ) : null}
          </div>
        )}
      </div>
    </div>
  );
});

export default IncomeDetails;
