import AclGuard from "components/AclGuard";
import EditableTableV2, {
  Column,
  EditableComponentRef,
} from "components/EditableTableV2/EditableTableV2";
import ErrorDialog from "components/ErrorDialog/ErrorDialog";
import InputBox from "components/InputBox";
import ReadOnlyTable from "components/ReadOnlyTable/ReadOnlyTable";
import SimpleDropDown from "components/SimpleDropDown/SimpleDropDown";
import SkeletonCustom from "components/Skeleton/Skeleton";
import Text from "components/Text/Text";
import i18n from "i18n";
import { numberFormatter } from "locale";
import React, { forwardRef, useEffect, useRef, useState } from "react";
import { useParams } from "react-router-dom";
import CamService from "services/transaction/CamService";
import SimpleReactValidator from "simple-react-validator";
import { currencyFormatToNumber, getStandardValidator, messageReplace } from "utils/CommonUtil";
import {
  CAM_APPLICANT_TYPE_OPTIONS,
  INPUT_STYLE_TYPE,
  LOAN_PURPOSE_OPTIONS,
  PAGE_MODE,
  TCamApplicantType,
  YES_NO_OPTIONS,
} from "utils/Constant";
import { showCurrency } from "views/transaction/PageCAM/Helper";

export interface ISummaryRepaymentHistoryTablePageObj {
  stageUid: string;
  total: {
    outstandingAmount: number;
    emiAmount: number;
    roi: number;
  };
  // properties: ICamSinglePropertyValuationData[];
  // totalValuation: number | "";
}
interface IProps {
  data: ISummaryRepaymentHistoryTablePageObj;
  isLoading?: boolean;
}

export interface ICamRepaymentHistoryTableRowData {
  uid?: string;
  // customerName?: string;
  applicant?: {
    fullName?: string;
  };
  stage?: {
    applicant?: {
      applicantType?: TCamApplicantType | "";
    };
  };
  bank?: {
    name?: string; //bank name
    type?: {
      name?: string; //financier type
    };
  };
  smPurpose?: string;
  nameOfInstitution?: string;
  sanctionedAmount?: string | number;
  outstandingAmount?: string | number;
  balanceTenor?: string;
  emiAmount?: string;
  pastDefaults?: string;
  sanctionedTenor?: string;
  noOfBounces?: string;
  roi?: string | number;
  emiPaid?: string | number;
  obligated?: string;
  highestDpd?: string | number;
  highestDpdMonth?: string;
  overdueAmount?: string | number;
  remarks?: string;
  smConsideredForBt?: string;
  isActiveCam?: boolean;
}

export const initialSummaryRepaymentHistoryPageObj: ISummaryRepaymentHistoryTablePageObj = {
  stageUid: "",
  total: {
    outstandingAmount: 0,
    emiAmount: 0,
    roi: 0,
  },
};

const RepaymentHistoryTable: React.FC<IProps> = forwardRef((props, ref) => {
  const { data, isLoading } = props;
  const params: { loanUid?: string; stageUid?: string } = useParams();
  /*
    -------------------------------------------------------
      State
    -------------------------------------------------------
    */
  const [pageObj, setPageObj] = useState<ISummaryRepaymentHistoryTablePageObj>({
    ...initialSummaryRepaymentHistoryPageObj,
  });
  const [pageMode, setPageMode] = useState(PAGE_MODE.VIEW);
  const [toastMessage, setToastMessage] = useState("");
  const [triggerToast, setTriggerToast] = useState(false);
  const [errorMessage, setErrorMessage] = useState("");
  const [showDialogAPIError, setShowDialogAPIError] = useState(false);
  // ref
  const toastMessageRef = useRef(null);
  const tableRef = React.useRef<EditableComponentRef>(null);

  // --------------------------- validator initialization ---------------------- //
  const [, forceUpdate] = useState<boolean>(false);

  const simpleValidator = useRef(
    new SimpleReactValidator({
      validators: { ...getStandardValidator() },
      ...messageReplace(),
      autoForceUpdate: { forceUpdate: () => forceUpdate((prev) => !prev) },
    })
  );

  // --------------------------- global variables ---------------------- //

  /*
     -------------------------------------------------------
       useEffect
     -------------------------------------------------------
   */

  useEffect(() => {
    if (data?.stageUid) {
      setPageObj((prevObj) => ({
        ...prevObj,
        ...data,
      }));
      // when add the properties  add the  validations for the properties
      // resetAllSectionValidations(data?.properties?.length);
    }
  }, [data]);

  useEffect(() => {
    if (errorMessage) {
      setShowDialogAPIError(true);
    }
  }, [errorMessage]);

  /*
    -------------------------------------------------------
      Service Calls
    -------------------------------------------------------
  */

  /*
    -------------------------------------------------------
      Handlers
    -------------------------------------------------------
  */

  const handleChange = (e: any) => {
    setPageObj((prevObj) => ({
      ...prevObj,
      [e.target.name]: e.target.value,
    }));
  };

  const handleTableFieldChange = (
    e: any,
    options: {
      rowData: ICamRepaymentHistoryTableRowData;
      field: keyof ICamRepaymentHistoryTableRowData;
      currency?: boolean;
    }
  ) => {
    let rowDataObj: ICamRepaymentHistoryTableRowData = { ...options.rowData };
    if (options.currency) {
      const unformattedValue = currencyFormatToNumber(e.target.value);
      rowDataObj = {
        ...rowDataObj,
        [`${options.field}`]: numberFormatter(unformattedValue, "en-IN", 0),
      };
      // updateTotalOfNetAppraisedMonthlyIncome(options.field);
    } else {
      rowDataObj = {
        ...rowDataObj,
        [`${options.field}`]: e.target.value,
      };
    }
    tableRef.current?.reportDataChange(rowDataObj);
  };

  /* 
    --------------------------------------------------------
    Validators:
    --------------------------------------------------------
  */

  const validate = (e: any) => {
    simpleValidator?.current?.showMessageFor(e?.target?.name);
  };

  const validateForm = () => {
    let isValid = false;
    isValid = simpleValidator?.current?.allValid() ?? false;
    if (isValid) {
      return isValid;
    } else {
      forceUpdate((prev) => !prev);
      simpleValidator?.current?.showMessages();
      forceUpdate((prev) => !prev);
      return false;
    }
  };

  /*
    -------------------------------------------------------
      Button Click Handlers
    -------------------------------------------------------
  */

  /*
    -------------------------------------------------------
      Helper Functions
    -------------------------------------------------------
  */

  /*
    -------------------------------------------------------
      Sectional Rendering
    -------------------------------------------------------
  */
  const renderApplicantType = (rowData: ICamRepaymentHistoryTableRowData) => {
    return (
      <div className="pt-4">
        <SimpleDropDown
          hideLabel
          placeholder="placeholder.cam.applicantType"
          name={`applicantType${rowData?.uid}`}
          value={rowData?.stage?.applicant?.applicantType}
          variant={INPUT_STYLE_TYPE.PRIMARY}
          options={CAM_APPLICANT_TYPE_OPTIONS}
          optionLabel="name"
          optionValue="code"
          disabled
        />
      </div>
    );
  };

  const renderCustomerName = (rowData: ICamRepaymentHistoryTableRowData) => {
    return (
      <div className="pt-4">
        <InputBox
          name={`customerName${rowData?.uid}`}
          variant={INPUT_STYLE_TYPE.SECONDARY}
          value={`${rowData?.applicant?.fullName ?? ""}`}
          disabled
          hideLabel
        />
      </div>
    );
  };

  const renderSource = (rowData: ICamRepaymentHistoryTableRowData) => {
    return (
      <div className="pt-4">
        <SimpleDropDown
          hideLabel
          placeholder="placeholder.cam.applicantType"
          name={`source${rowData?.uid}`}
          value={rowData?.bank?.type?.name}
          variant={INPUT_STYLE_TYPE.PRIMARY}
          options={rowData?.bank?.name ? [`${rowData?.bank?.type?.name}`] : []}
          disabled
        />
      </div>
    );
  };

  const renderPurpose = (rowData: ICamRepaymentHistoryTableRowData) => {
    return (
      <div className="pt-4">
        <SimpleDropDown
          hideLabel
          placeholder="placeholder.cam.purposeOfLoan"
          name={`purpose${rowData?.uid}`}
          value={rowData?.smPurpose}
          variant={INPUT_STYLE_TYPE.PRIMARY}
          options={LOAN_PURPOSE_OPTIONS}
          optionLabel="name"
          optionValue="value"
          disabled
        />
      </div>
    );
  };

  const renderNameOfInstitute = (rowData: ICamRepaymentHistoryTableRowData) => {
    return (
      <div className="pt-4">
        <InputBox
          name={`nameOfInstitute${rowData?.uid}`}
          variant={INPUT_STYLE_TYPE.SECONDARY}
          value={rowData?.bank?.name ?? ""}
          disabled
          hideLabel
        />
      </div>
    );
  };

  const renderSanctionAmount = (rowData: ICamRepaymentHistoryTableRowData) => {
    return (
      <div className="pt-4">
        <InputBox
          name={`sanctionAmount${rowData?.uid}`}
          variant={INPUT_STYLE_TYPE.SECONDARY}
          className="text-right"
          value={rowData?.sanctionedAmount}
          disabled
          hideLabel
        />
      </div>
    );
  };

  const renderOutstandingAmount = (rowData: ICamRepaymentHistoryTableRowData) => {
    return (
      <div className="pt-4">
        <InputBox
          name={`outstandingAmount${rowData?.uid}`}
          variant={INPUT_STYLE_TYPE.SECONDARY}
          className="text-right"
          value={rowData?.outstandingAmount}
          disabled
          hideLabel
        />
      </div>
    );
  };

  const renderBalanceTenor = (rowData: ICamRepaymentHistoryTableRowData) => {
    return (
      <div className="pt-4">
        <InputBox
          name={`balanceTenor${rowData?.uid}`}
          variant={INPUT_STYLE_TYPE.SECONDARY}
          className="text-right"
          value={rowData?.balanceTenor}
          disabled
          hideLabel
        />
      </div>
    );
  };

  const renderEmi = (rowData: ICamRepaymentHistoryTableRowData) => {
    return (
      <div className="pt-4">
        <InputBox
          name={`emi${rowData?.uid}`}
          variant={INPUT_STYLE_TYPE.SECONDARY}
          className="text-right"
          value={rowData?.emiAmount}
          disabled
          hideLabel
        />
      </div>
    );
  };

  const renderPastDefaults = (rowData: ICamRepaymentHistoryTableRowData) => {
    return (
      <div className="pt-4">
        <InputBox
          name={`pastDefault${rowData?.uid}`}
          placeholder="placeholder.cam.pastDefaults"
          variant={INPUT_STYLE_TYPE.SECONDARY}
          className="text-right"
          value={rowData?.pastDefaults}
          disabled
          hideLabel
        />
      </div>
    );
  };

  const renderSanctionTenor = (rowData: ICamRepaymentHistoryTableRowData) => {
    return (
      <div className="pt-4">
        <InputBox
          name={`sanctionTenor${rowData?.uid}`}
          variant={INPUT_STYLE_TYPE.SECONDARY}
          className="text-right"
          value={rowData?.sanctionedTenor}
          hideLabel
          disabled
        />
      </div>
    );
  };

  const renderNoOfBounces = (rowData: ICamRepaymentHistoryTableRowData) => {
    return (
      <div className="pt-4">
        <InputBox
          name={`noOfBounces${rowData?.uid}`}
          variant={INPUT_STYLE_TYPE.SECONDARY}
          className="text-right"
          value={rowData?.noOfBounces}
          hideLabel
          disabled
        />
      </div>
    );
  };

  const renderRoi = (rowData: ICamRepaymentHistoryTableRowData) => {
    return (
      <div className="pt-4">
        <InputBox
          name={`roi${rowData?.uid}`}
          variant={INPUT_STYLE_TYPE.SECONDARY}
          className="text-right"
          value={rowData?.roi}
          hideLabel
          disabled
        />
      </div>
    );
  };

  const renderEmiPaid = (rowData: ICamRepaymentHistoryTableRowData) => {
    return (
      <div className="pt-4">
        <InputBox
          name={`emiPaid${rowData?.uid}`}
          variant={INPUT_STYLE_TYPE.SECONDARY}
          className="text-right"
          value={rowData?.emiPaid}
          hideLabel
        />
      </div>
    );
  };

  const renderObligated = (rowData: ICamRepaymentHistoryTableRowData) => {
    return (
      <div className="pt-4">
        <SimpleDropDown
          hideLabel
          placeholder="placeholder.cam.obligated"
          name={`obligated${rowData?.uid}`}
          value={rowData?.obligated}
          variant={INPUT_STYLE_TYPE.PRIMARY}
          options={YES_NO_OPTIONS}
          optionLabel="label"
          optionValue="value"
          disabled
        />
      </div>
    );
  };

  const renderHighestDpds = (rowData: ICamRepaymentHistoryTableRowData) => {
    return (
      <div className="pt-4">
        <InputBox
          name={`highestDpd${rowData?.uid}`}
          variant={INPUT_STYLE_TYPE.SECONDARY}
          className="text-right"
          value={rowData?.highestDpd}
          // hideErrorRow
        />
      </div>
    );
  };

  const renderHighestDpdsMonth = (rowData: ICamRepaymentHistoryTableRowData) => {
    return (
      <div className="pt-4">
        <InputBox
          name={`highestDpdMonth${rowData?.uid}`}
          variant={INPUT_STYLE_TYPE.SECONDARY}
          className="text-right"
          value={rowData?.highestDpdMonth}
          // onChange={(e) => handleTableFieldChange(e, { rowData, field: "highestDpdMonth" })}
          // onBlur={validate}
          // // loader={isLoading}
          // validation={simpleValidator?.current?.message(
          //   `highestDpdMonth${rowData?.uid}`,
          //   rowData.highestDpdMonth,
          //   "mandatory|checkNumber|onlyPositive"
          // )}
          hideLabel
          // hideErrorRow
        />
      </div>
    );
  };

  const renderOverDueAmount = (rowData: ICamRepaymentHistoryTableRowData) => {
    return (
      <div className="pt-4">
        <InputBox
          name={`overdueAmount${rowData?.uid}`}
          variant={INPUT_STYLE_TYPE.SECONDARY}
          className="text-right"
          value={rowData?.overdueAmount}
          hideLabel
          disabled
        />
      </div>
    );
  };

  const renderRemarkOrClarification = (rowData: ICamRepaymentHistoryTableRowData) => {
    return (
      <div className="pt-4">
        <InputBox
          name={`remarks${rowData?.uid}`}
          variant={INPUT_STYLE_TYPE.SECONDARY}
          value={rowData?.remarks}
          hideLabel
          disabled
          // hideErrorRow
        />
      </div>
    );
  };

  const renderConsideredForBt = (rowData: ICamRepaymentHistoryTableRowData) => {
    return (
      <div className="pt-4">
        <SimpleDropDown
          hideLabel
          placeholder="placeholder.cam.consideredForBt"
          name={`smConsideredForBt${rowData?.uid}`}
          value={rowData?.smConsideredForBt}
          variant={INPUT_STYLE_TYPE.PRIMARY}
          options={YES_NO_OPTIONS}
          optionLabel="label"
          optionValue="value"
          disabled
        />
      </div>
    );
  };

  const renderTotal = () => {
    return (
      <Text
        className=" font-medium text-primary-ptext-1000  "
        label={`label.cibilDetails.enquirySummary.thead.total/avg`}
      />
    );
  };

  const renderTotalOfOutStandingAmount = () => {
    const total = showCurrency(pageObj?.total?.outstandingAmount);
    return (
      <Text
        className=" font-medium text-primary-ptext-1000 text-right"
        label={pageObj?.total?.outstandingAmount > 0 ? `${total}` : ""}
      />
    );
  };

  const renderTotalOfEmi = () => {
    const total = showCurrency(pageObj?.total?.emiAmount);
    return (
      <Text
        className=" font-medium text-primary-ptext-1000 text-right"
        label={pageObj?.total?.emiAmount > 0 ? `${total}` : ""}
      />
    );
  };

  const renderTotalOfRoi = () => {
    const total = showCurrency(pageObj?.total?.roi);
    return (
      <Text
        className=" font-medium text-primary-ptext-1000 text-right"
        label={pageObj?.total?.roi > 0 ? `${total}` : ""}
      />
    );
  };

  /*
    -------------------------------------------------------
    Main Rendering
    -------------------------------------------------------
  */
  return (
    <>
      <div>
        <AclGuard override={{ read: pageMode === PAGE_MODE.VIEW }}>
          <div className=" grid grid-cols-4 gap-3  ">
            <div className="col-span-4">
              {isLoading ? (
                <SkeletonCustom
                  height="19.375rem"
                  width="100%"
                  borderRadius="8px"
                  animation="wave"
                />
              ) : (
                  <ReadOnlyTable
                    ref={tableRef}
                    uniqueKey="uid"
                    standardDataFormatPromise={CamService.getRepaymentHistoryTable}
                    search={`&loan.uid=${params?.loanUid}&isActiveCam=true`}
                    mode={"UPDATE"}
                  >
                    <Column
                      header={i18n.t("label.cam.typeOfApplicant")}
                      className="text-primary-ptext-1000 text-left text-xs h-12 pl-6"
                      body={renderApplicantType}
                      footer={renderTotal}
                    />
                    <Column
                      header={i18n.t("label.cam.customerName")}
                      className="text-primary-ptext-1000 text-left text-xs h-12 pl-6"
                      body={renderCustomerName}
                    />
                    <Column
                      header={i18n.t("label.cam.source(Bank/Financier)")}
                      className="table-header-right text-primary-ptext-1000 text-xs h-12 pl-6"
                      body={renderSource}
                    />
                    <Column
                      header={i18n.t("label.cam.purpose(home/personal)")}
                      className="table-header-right text-primary-ptext-1000 text-xs h-12 pl-6"
                      body={renderPurpose}
                    />
                    <Column
                      header={i18n.t("label.cam.nameOfInstitution")}
                      className=" text-primary-ptext-1000 text-xs h-12 pl-6"
                      body={renderNameOfInstitute}
                    />
                    <Column
                      header={i18n.t("label.cam.sanctionAmount")}
                      className="table-header-right text-primary-ptext-1000 text-xs h-12 pl-6"
                      body={renderSanctionAmount}
                    />

                    <Column
                      header={i18n.t("label.cam.outstandingAmount")}
                      className="table-header-right text-primary-ptext-1000 text-xs h-12 pl-6"
                      body={renderOutstandingAmount}
                      footer={renderTotalOfOutStandingAmount}
                      footerClassName=" text-right"
                    />

                    <Column
                      header={i18n.t("label.cam.balanceTenor")}
                      className="table-header-right text-primary-ptext-1000 text-xs h-12 pl-6"
                      body={renderBalanceTenor}
                    />

                    <Column
                      header={i18n.t("label.cam.emi()")}
                      className="table-header-right text-primary-ptext-1000 text-xs h-12 pl-6"
                      body={renderEmi}
                      footer={renderTotalOfEmi}
                      footerClassName=" text-right"
                    />
                    {/* done */}
                    <Column
                      header={i18n.t("label.cam.pastDefaults")}
                      className="table-header-right text-primary-ptext-1000 text-xs h-12 pl-6"
                      body={renderPastDefaults}
                    />
                    <Column
                      header={i18n.t("label.cam.sanctionTenor")}
                      className="table-header-right text-primary-ptext-1000 text-xs h-12 pl-6"
                      body={renderSanctionTenor}
                    />
                    <Column
                      header={i18n.t("label.cam.noOfBounces")}
                      className="table-header-right text-primary-ptext-1000 text-xs h-12 pl-6"
                      body={renderNoOfBounces}
                    />
                    <Column
                      header={i18n.t("label.cam.roi(%)")}
                      className="table-header-right text-primary-ptext-1000 text-xs h-12 pl-6"
                      body={renderRoi}
                      footer={renderTotalOfRoi}
                      footerClassName=" text-right"
                    />
                    <Column
                      header={i18n.t("label.cam.emiPaid")}
                      className="table-header-right text-primary-ptext-1000 text-xs h-12 pl-6"
                      body={renderEmiPaid}
                    />
                    <Column
                      header={i18n.t("label.cam.obligated(y/n)")}
                      className=" text-primary-ptext-1000 text-xs h-12 pl-6 min-w-30"
                      body={renderObligated}
                    />
                    <Column
                      header={i18n.t("label.cam.highestDpd's")}
                      className="table-header-right text-primary-ptext-1000 text-xs h-12 pl-6"
                      body={renderHighestDpds}
                    />
                    <Column
                      header={i18n.t("label.cam.highestDpd'sMonth")}
                      className="table-header-right text-primary-ptext-1000 text-xs h-12 pl-6"
                      body={renderHighestDpdsMonth}
                    />
                    <Column
                      header={i18n.t("label.cam.overdueAmount()")}
                      className="table-header-right text-primary-ptext-1000 text-xs h-12 pl-6"
                      body={renderOverDueAmount}
                    />
                    <Column
                      header={i18n.t("label.cam.remarks/clarifications")}
                      className=" text-primary-ptext-1000 text-xs h-12 pl-6"
                      body={renderRemarkOrClarification}
                    />
                    <Column
                      header={i18n.t("label.cam.considerForBt(yes/no)")}
                      className=" text-primary-ptext-1000 text-xs h-12 px-6"
                      body={renderConsideredForBt}
                    />
                    {/* <Column
                      header={i18n.t("label.cam.action")}
                      className="text-primary-ptext-1000 text-right text-xs h-12 px-6 column-shadow-right"
                      style={{ minWidth: "50px", width: "50px" }}
                      body={renderActions}
                      alignFrozen="right"
                      frozen
                    /> */}
                  </ReadOnlyTable>
              )}
            </div>
          </div>
        </AclGuard>
      </div>

      <ErrorDialog
        onHide={() => {
          setShowDialogAPIError(false);
          setErrorMessage("");
        }}
        visible={showDialogAPIError}
        errorMessage={errorMessage}
      />
    </>
  );
});

export default RepaymentHistoryTable;
