import ButtonBox from "components/ButtonBox/ButtonBox";
import DialogBox from "components/Dialog";
import ErrorDialog from "components/ErrorDialog/ErrorDialog";
import IconButton, { BUTTON_TYPES } from "components/IconButton/IconButton";
import InputTextArea from "components/InputTextArea/InputTextArea";
import Label from "components/Label";
import ReadOnlyTable, { IReadonlyTableRef } from "components/ReadOnlyTable/ReadOnlyTable";
import SimpleOverlayPanel from "components/SimpleOverlayPanel/SimpleOverlayPanel";
import Text from "components/Text/Text";
import TextButton from "components/TextButton/TextButton";
import ToastCustom from "components/Toast";
import i18n from "i18n";
import { Column } from "primereact/column";
import { BaseSyntheticEvent, useEffect, useRef, useState } from "react";
import { useParams } from "react-router-dom";
import CreditQueryService from "services/transaction/CreditQueryService";
import SimpleReactValidator from "simple-react-validator";
import {
  formatDate,
  getStandardValidator,
  messageReplace,
  parseStandardAPIErrorMessage,
  validatePutAPIResponse,
} from "utils/CommonUtil";
import { BUTTON_TYPE, STATIC_MASTER_TYPE_TOAST_TEXT, TOAST_STYLE_TYPE } from "utils/Constant";

interface IDialogData {
  uid: string | null;
  stage: string;
  query: string;
  raisedOn: string;
  raisedBy: string;
  updatedOn: string;
  updatedBy: string;
  status: string;
  reply: string;
  action: string;
}

const initialDialogData: IDialogData = {
  uid: "",
  stage: "",
  query: "",
  raisedOn: "",
  raisedBy: "",
  updatedOn: "",
  updatedBy: "",
  status: "",
  reply: "",
  action: "",
};

const STAGE_NAMES: { [key: string]: string } = {
  IN_PRINCIPAL_APPROVAL: "In Principal Approval",
  FINANCIAL_APPROVAL: "Financial Approval",
  FINAL_APPROVAL: "Final Approval",
  CAM_AND_RECOMMENDATION:"CAM and Recommendation"
};

const LOAN_QUERY_STATUS: { [key: string]: string } = {
  IN_PROGRESS: "In Progress",
  CLOSED: "Closed",
};
const STATUS = {
  IN_PROGRESS: "IN_PROGRESS",
  CLOSED: "CLOSED",
};

const CreditQuery = () => {
  const params: { loanUid?: string } = useParams();

  const [dialogInfo, setDialogInfo] = useState<IDialogData>(initialDialogData);
  const [showQueryBox, setShowQueryBox] = useState<boolean>(false);

  // error handling states
  const [toastMessage, setToastMessage] = useState("");
  const [triggerToast, setTriggerToast] = useState(false);
  const [errorMessage, setErrorMessage] = useState("");
  const [showDialogAPIError, setShowDialogAPIError] = useState(false);

  // --------------------- validator initialization ---------------------- //
  const [, forceUpdate] = useState<boolean>(false); //NOSONAR

  const simpleValidator = useRef(
    new SimpleReactValidator({
      validators: { ...getStandardValidator() },
      ...messageReplace(),
      autoForceUpdate: { forceUpdate: () => forceUpdate((prev: boolean) => !prev) },
    })
  );

  /*---------------refs----------------------- */
  const dataTableRef = useRef<IReadonlyTableRef>(null);
  const toastMessageRef = useRef(null);
  const overlayPanelRef = useRef(null);

  /* --------------------------------Hooks---------------------------------------------------- */

  useEffect(() => {
    if (params.loanUid) {
      init();
    }
  }, [params.loanUid]);

  useEffect(() => {
    if (dialogInfo.uid) {
      setShowQueryBox(true);
    }
  }, [dialogInfo]);

  useEffect(() => {
    if (toastMessageRef?.current && toastMessage !== "") {
      // @ts-ignore: will be fixed in future
      toastMessageRef?.current?.showFunction();
      setToastMessage("");
    }
  }, [toastMessage, triggerToast]);

  useEffect(() => {
    if (errorMessage) {
      setShowDialogAPIError(true);
    }
  }, [errorMessage]);

  /*-----------------------------Service Calls------------------- */

  const init = async () => {
    dataTableRef?.current?.search();
  };

  /* -------------------------Event Handlers -> OnClick, onToggle etc----------------------- */

  const onViewComplete = (rowInfo: any) => {
    const info = {
      uid: rowInfo?.uid ?? null,
      stage: rowInfo?.stage?.stageName ?? "",
      query: rowInfo?.queryMessage ?? "",
      raisedOn: rowInfo?.createdAt ?? new Date().toISOString(),
      raisedBy: rowInfo?.createdByUserName ?? "",
      updatedOn: rowInfo?.updatedAt ?? new Date().toISOString(),
      updatedBy: rowInfo?.updatedByUserName ?? "",
      status: LOAN_QUERY_STATUS[`${rowInfo?.status}`] ?? "",
      reply: rowInfo?.queryResponse ?? "",
      action: "",
    };

    setDialogInfo(info);
  };
  const onViewDetails = (e: any) => {
    if (overlayPanelRef.current) {
      // @ts-ignore: Will fix later
      overlayPanelRef.current?.toggle(e);
    }
  };
  const onViewInProgress = (rowInfo: any) => {
    const info = {
      uid: rowInfo?.uid ?? null,
      stage: rowInfo?.stage?.stageName ?? "",
      query: rowInfo?.queryMessage ?? "",
      raisedOn: rowInfo?.createdAt ?? new Date().toISOString(),
      raisedBy: rowInfo?.createdByUserName ?? "",
      updatedOn: rowInfo?.updatedAt ?? new Date().toISOString(),
      updatedBy: rowInfo?.updatedByUserName ?? "",
      status: LOAN_QUERY_STATUS[`${rowInfo?.status}`] ?? "",
      reply: "",
      action: "",
    };

    setDialogInfo(info);
  };

  const onCancel = () => {
    setShowQueryBox(false);
    resetValidator();
  };

  const onSubmit = async () => {
    if (!validateForm()) {
      return;
    }

    const payload = {
      uid: dialogInfo.uid,
      status: STATUS.CLOSED,
      queryResponse: dialogInfo.reply,
    };

    try {
      const res = await CreditQueryService.updateQueryResponse(payload);

      if (validatePutAPIResponse(res)) {
        dataTableRef?.current?.search();
        setToastMessage(STATIC_MASTER_TYPE_TOAST_TEXT.SAVED);
        setTriggerToast(!triggerToast);
        setShowQueryBox(false);
      }
    } catch (error) {
      console.log(error);
      setShowQueryBox(false);
      resetValidator();
      setErrorMessage(parseStandardAPIErrorMessage(error));
    }
  };

  const handleChange = (e: BaseSyntheticEvent) => {
    setDialogInfo((prev) => ({
      ...prev,
      reply: e.target.value,
    }));
  };

  /* 
    --------------------------------------------------------------------------------------------------
    Validators:
    --------------------------------------------------------------------------------------------------
  */

  const validate = (e: any) => {
    simpleValidator?.current?.showMessageFor(e?.target?.name);
  };

  const validateForm = () => {
    let isValid = false;

    isValid = simpleValidator?.current?.allValid();
    if (isValid) {
      return isValid;
    } else {
      forceUpdate((prev) => !prev);
      simpleValidator?.current?.showMessages();
      forceUpdate((prev) => !prev);
      return false;
    }
  };

  const resetValidator = () => {
    simpleValidator.current = new SimpleReactValidator({
      validators: { ...getStandardValidator() },
      ...messageReplace(),
      autoForceUpdate: { forceUpdate: () => forceUpdate((prev) => !prev) },
    });
    simpleValidator?.current?.hideMessages();
  };

  /* ----------------------Sectional Renders----------------------------- */

  const renderHeaderStage = () => {
    return (
      <div className="flex">
        <Label label={`${i18n.t("label.cq.stage")}`} className="mr-1" />
      </div>
    );
  };

  const renderStage = (rowData: any) => {
    return (
      <>
        <div className="text-primary-ptext-1000 text-xs text-left w-30">
          {STAGE_NAMES[`${rowData?.stage?.stageName}`] ?? ""}
        </div>
      </>
    );
  };

  const renderHeaderQuery = () => {
    return (
      <div className="flex">
        <Label label={`${i18n.t("label.cq.query")}`} className="mr-1" />
      </div>
    );
  };

  const renderQuery = (rowData: any) => {
    return (
      <>
        <div
          className="text-primary-ptext-1000 text-xs text-left "
          dangerouslySetInnerHTML={{ __html: rowData.queryMessage }}
        />
      </>
    );
  };

  const renderHeaderRaisedOn = () => {
    return (
      <div className="flex">
        <Label label={`${i18n.t("label.cq.raisedOn")}`} className="mr-1" />
      </div>
    );
  };

  const renderRaisedOn = (rowData: any) => {
    return (
      <>
        <div className="text-primary-ptext-1000 text-xs text-left w-30">
          {formatDate(rowData?.createdAt) ?? "-"}
        </div>
      </>
    );
  };

  const renderHeaderRaisedBy = () => {
    return (
      <div className="flex">
        <Label label={`${i18n.t("label.cq.raisedBy")}`} className="mr-1" />
      </div>
    );
  };

  const renderRaisedBy = (rowData: any) => {
    return (
      <>
        <div className="text-primary-ptext-1000 text-xs text-left w-30">
          {rowData?.createdByUserName ?? ""}
        </div>
      </>
    );
  };

  const renderHeaderRespondedOn = () => {
    return (
      <div className="flex">
        <Label label={`${i18n.t("label.cq.respondedOn")}`} className="mr-1" />
      </div>
    );
  };

  const renderRespondedOn = (rowData: any) => {
    return (
      <>
        <div className="text-primary-ptext-1000 text-xs text-left w-30">
          {formatDate(rowData?.updatedAt) ?? "-"}
        </div>
      </>
    );
  };

  const renderHeaderRespondedBy = () => {
    return (
      <div className="flex">
        <Label label={`${i18n.t("label.cq.respondedBy")}`} className="mr-1" />
      </div>
    );
  };

  const renderRespondedBy = (rowData: any) => {
    return (
      <>
        <div className="text-primary-ptext-1000 text-xs text-left w-30">
          {rowData?.updatedByUserName ?? ""}
        </div>
      </>
    );
  };

  const renderHeaderStatus = () => {
    return (
      <div>
        <Label label={`${i18n.t("label.cq.status")}`} className="mr-1" />
      </div>
    );
  };

  const renderStatus = (rowData: any) => {
    return (
      <>
        <div className="text-primary-ptext-1000 text-xs text-left">
          {LOAN_QUERY_STATUS[`${rowData?.status}`] ?? ""}
        </div>
      </>
    );
  };

  const renderHeaderAction = () => {
    return (
      <div>
        <Label label={`${i18n.t("label.cq.action")}`} className="mr-1" />
      </div>
    );
  };

  const renderAction = (rowData: any) => {
    return (
      <>
        <div className="flex justify-center items-center text-primary-ptext-1000 text-xs cursor-pointer">
          {rowData.status === STATUS.CLOSED ? (
            <IconButton
              className={`select-none`}
              buttonType={BUTTON_TYPES.VIEW}
              onClick={() => {
                onViewComplete(rowData);
              }}
            />
          ) : (
            <IconButton
              className={`select-none`}
              buttonType={BUTTON_TYPES.REPLY}
              onClick={() => onViewInProgress(rowData)}
            />
          )}
        </div>
      </>
    );
  };

  const renderLoanDetail = () => {
    return (
      <div className="flex flex-col gap-4 p-5 ">
        <div className="flex flex-row justify-between items-center  ">
          <div className="text-sm text-secondary-stext-900">Assigned to :</div>
          <div className="text-sm text-secondary-stext-900 font-bold ml-2">
            {/* {loanDetail?.product} */}
          </div>
        </div>
        <div className="flex flex-row justify-between items-center  ">
          <div className="text-sm text-secondary-stext-900">Delegated to :</div>
          <div className="text-sm text-secondary-stext-900 font-bold ml-2">
            {/* {loanDetail?.product} */}
          </div>
        </div>
        <div className="flex flex-row justify-between items-center  ">
          <div className="text-sm text-secondary-stext-900">Date of Initiation :</div>
          <div className="text-sm text-secondary-stext-900 font-bold ml-2">
            {/* {loanDetail?.product} */}
          </div>
        </div>
      </div>
    );
  };

  return (
    <div className=" flex flex-col gap-8">

      {/* Table  */}
      <div className=" bg-white rounded-xl pt-6 px-7 pb-7">
        {params.loanUid && (
          <div>
            <ReadOnlyTable
              ref={dataTableRef}
              standardDataFormatPromise={CreditQueryService.getAll}
              search={`&loan.uid=${params.loanUid}&isActive=${true}`}
              dataKey="uid"
              style={{ width: "100%" }}
            >
              <Column
                className="text-primary-ptext-1000 text-left text-xs h-12 px-4"
                header={renderHeaderStage}
                body={renderStage}
              />
              <Column
                className="text-primary-ptext-1000 text-left text-xs h-12 px-4"
                header={renderHeaderQuery}
                body={renderQuery}
              />
              <Column
                className="text-primary-ptext-1000 text-left text-xs h-12 px-4"
                header={renderHeaderRaisedOn}
                body={renderRaisedOn}
              />
              <Column
                className="text-primary-ptext-1000 text-left text-xs h-12 px-4"
                header={renderHeaderRaisedBy}
                body={renderRaisedBy}
              />

              <Column
                className="text-primary-ptext-1000 text-left text-xs h-12 px-4"
                header={renderHeaderRespondedOn}
                body={renderRespondedOn}
              />
              <Column
                className="text-primary-ptext-1000 text-left text-xs h-12 px-4"
                header={renderHeaderRespondedBy}
                body={renderRespondedBy}
              />
              <Column
                className="text-primary-ptext-1000 text-left text-xs h-12 px-4"
                header={renderHeaderStatus}
                body={renderStatus}
              />
              <Column
                className="text-primary-ptext-1000 text-xs h-12 pr-4"
                header={renderHeaderAction}
                body={renderAction}
              />
            </ReadOnlyTable>
          </div>
        )}
      </div>

      <DialogBox className={`min-w-[780px] !p-0`} visible={showQueryBox} closable={false}>
        <div className="p-6">
          <div className="mb-6 flex justify-between">
            <div className="flex flex-col gap-2.5">
              <div className="text-base font-bold text-primary-primaryTextColor">
                <Text label={"label.cq.responseToQuery"} />
              </div>
              <div className="flex items-center gap-2 text-xs">
                <div>
                  <span className="font-medium">
                    <Text label={"label.cq.raisedOn"} />
                  </span>
                  <span className="ml-2">{formatDate(dialogInfo.raisedOn)}</span>
                </div>
                <div>
                  <span className="font-medium">
                    <Text label={"label.cq.raisedBy"} />
                  </span>
                  <span className="ml-2">{dialogInfo.raisedBy}</span>
                </div>
              </div>
            </div>
            <div className="self-start flex items-center gap-5 text-xs text-primary-ptext-1000">
              <div>
                <ButtonBox
                  className=""
                  buttonType={BUTTON_TYPE.CANCEL_OUTLINE}
                  onClick={onCancel}
                />
              </div>
              {dialogInfo.status === LOAN_QUERY_STATUS.IN_PROGRESS && (
                <div>
                  <ButtonBox
                    className=""
                    buttonType={BUTTON_TYPE.SUBMIT_OUTLINE}
                    onClickWithLoader={onSubmit}
                    disabled
                  />
                </div>
              )}
            </div>
          </div>

          <div className="flex flex-col">
            <div className="h-40">
              <InputTextArea
                name="query"
                label={"label.cq.query"}
                value={dialogInfo.query}
                autoResize
                maxCount={200}
                disabled
              />
            </div>

            <div className="mb-3 flex items-center gap-2 text-xs">
              <div>
                <span className="font-medium">
                  <Text label={"label.cq.updatedOn"} />
                </span>
                <span className="ml-2">{formatDate(dialogInfo.updatedOn)}</span>
              </div>
              <div>
                <span className="font-medium">
                  <Text label={"label.cq.updatedBy"} />
                </span>
                <span className="ml-2">{dialogInfo.updatedBy}</span>
              </div>
            </div>

            <div className="h-40">
              <InputTextArea
                name="reply"
                label={"label.cq.reply"}
                onBlur={validate}
                validation={simpleValidator?.current?.message(
                  `reply`,
                  dialogInfo.reply,
                  "mandatory"
                )}
                value={dialogInfo.reply}
                maxCount={200}
                onChange={handleChange}
                autoResize
                disabled
              />
            </div>
          </div>
        </div>
      </DialogBox>

      <ToastCustom
        position={"bottom-right"}
        ref={toastMessageRef}
        toastText={toastMessage}
        variant={TOAST_STYLE_TYPE.PRIMARY}
      />
      <SimpleOverlayPanel
        className="rounded-lg shadow-lg !w-80 p-1.5 "
        ref={overlayPanelRef}
        content={renderLoanDetail()}
      />
      <ErrorDialog
        onHide={() => {
          setShowDialogAPIError(false);
          setErrorMessage("");
        }}
        visible={showDialogAPIError}
        errorMessage={errorMessage}
      />
    </div>
  );
};

export default CreditQuery;
